<template>
  <div class="project" v-if="!isMobile()">
    <div id='projectHeadingdiv'>
      <p id='projectHeading'>Register a Project</p>
    </div>
    <p id="discription">To register a project for your team, enter the following information:</p>

    <form action="/#" method="post"> 
      <input v-model='projectName' id="projectnameinput" placeholder="Project Name">
      <br>
      <input v-model='projectTeam' type='text' name=description value="" id='projectdescriptioninput' placeholder="Project Team">
      <br>
    </form>
    <div id="next">
      <button id="projectButton" @click=updates()>Next </button>
    </div>
  </div>

<!-- Mobile User Interface -->
  <div class='projectMobile' v-else>
    <div id='projectHeadingdivMobile'>
      <p id='projectHeading'>Registering Project</p>
    </div>
    <p id="discriptionMobile">To register a project for your team, enter the following information:</p>

    <form action="/#" method="post"> 
      <input v-model='projectName' id="projectnameinput" placeholder="Project Name">
      <br>
      <input v-model='projectTeam' type='text' name=description value="" id='projectdescriptioninput' placeholder="Project Team">
      <br>
    </form>
    <div id="next">
      <button id="projectButton" @click=updates()>Next </button>
    </div>
  </div>
</template>

<script>
import axios from "axios"

  export default{
    name: 'project',
    data(){
      return{
        projectName:null,
        projectDescription:null,
        projectTeam:null
      }
    },
    mounted() {
      // to be consider what will doing here during mounted
    },
    methods: {
          checkInput(){
            return true;
          },
          async updates(){
            if (!this.projectName || !this.projectTeam) {
              alert("please enter your project name")
              return; // if the commit input is nothing, ignore
            }

            const projData = {
              name: this.projectName,
              team: this.projectTeam
            }

            axios.post("http://35.9.22.64:8080/projects/addProject", projData)
              .then(response => {
                console.log(response.data)
              })

            const responseTeam = await axios.get("http://35.9.22.64:8080/teams/getTeamByName/" + this.projectTeam)

            const proj = {
              project: this.projectName
            }

            axios.put("http://35.9.22.64:8080/teams/addProject/" + responseTeam.data.id, proj)


            this.savecommit();
            this.removeCurrentData();
            this.$router.push('RegisteringProducts')
          },
          savecommit() {
            //save to localStorage
            const parsed = JSON.stringify(this.projectName);
            localStorage.setItem('projectName', parsed);
            const parsedDescription = JSON.stringify(this.projectDescription);
            localStorage.setItem('projectDescription', parsedDescription);
          },
          removeCurrentData(){
            localStorage.removeItem('githubRepoName');
            localStorage.removeItem('githubRepoURL');
            localStorage.removeItem('githubUsername');
            localStorage.removeItem('githubAuthenticationToken');
            localStorage.removeItem('sonarqubeInstanceName');
            localStorage.removeItem('sonarqubeProjectURL');
            localStorage.removeItem('sonarqubeAuthenticationToken');
            localStorage.removeItem('jenkinsJobName');
            localStorage.removeItem('jenkinsProjectURL');
            localStorage.removeItem('jenkinsAuthenticationToken');
            localStorage.removeItem('githubStatus');
            localStorage.removeItem('incidentStatus');
            localStorage.removeItem('jenkinsStatus');
            localStorage.removeItem('sonarqubeStatus');
          },
          isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
            }
          }
      }
  }
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

#projectHeadingdiv{
  top:15%;
  width:100%;
  position:absolute;
  text-align: center;
  margin-bottom:50px;
}
#projectHeading{
  min-width: 500px;
  font-size:42px;
  font-family: 'Courier New', Courier, monospace;
}
form{
  position: absolute;
  top:32%;
  left:25%;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
  font-size:23px;
  text-align: center;
}
#projectnameinput{
  min-width: 600px;
  max-width: 800px;
  width:50%;
}
#projectdescriptioninput{
  min-width: 600px;
  max-width: 800px;
  width:50%;
}
#discription{
  position:absolute;
  top:25%;
  left:25%;
  font-family: 'Courier New', Courier, monospace;
}
#next{
  text-align: center;
}

#projectButton{
  min-width: 400px;
  max-width: 600px;
  top:60%;
  left:35%;
  width:30%;
  height:8%;
  position: absolute;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#projectButton:hover{
  box-shadow: 0 30px 30px #5c5c5c;
  top: 59.2%;
  left: 32%;
  width:36%;
  height:9.6%;
  font-size:18px;
  transition: 0.2s
}

/* Mobile User Interface Style */

.projectMobile form{
  position: absolute;
  top:32%;
  left:0%;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
  font-size:23px;
  text-align: center;
}

#projectnameinputMobile{
  position: relative;
  width:90%;
}
#projectdescriptioninputMobile{
  position: relative;
  width:90%;
}

#projectHeadingdivMobile{
  margin-top: 120px;
  margin-left:-20px;
  position: relative;
  text-align: center;
  margin-bottom:30px;
}
#projectHeadingdivMobile p{
  font-size:26px;
  font-weight: bolder;
}
#discriptionMobile{
  margin-left:10px;
}

#projectButtonMobile{
  top:80%;
  left:25%;
  width:50%;
  height:8%;
  position: absolute;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}

.projectMobile #projectButton{
  position:absolute;
  top:70%;
  width:30%;
  height:10%;
  left:10px;
}
</style>
