<template>
    <div class="register">
        
        <div class="image" @click="goMainPage()">
            <img src="../assets/smallf.png" width="200vw">
        </div>

        <form @submit.prevent="pressed">
            <div class="login">
                <input type="email" placeholder="Login" v-model="email">
            </div>

            <div class="password">
                <input type="password" v-model="password" placeholder="Password">
            </div>

            <button type="submit">Login</button>
            
        </form>

        <span>Need an account? Click here to <router-link to="/UserRegister">register</router-link></span>

        <div class="error" v-if="error">{{error.message}}</div>

    </div>
</template>

<script>
import * as firebase from "firebase/app"
import "firebase/auth"

export default {
    data() {
        return {
            email: "",
            password: "",
            error: ""
        }
    },
    methods: {
        async pressed() {
            try {
                const val = await firebase.auth().signInWithEmailAndPassword(this.email, this.password)
                console.log(val)
                this.$router.push({name: "Main"});
            }catch(err) {
                alert("Invalid username and/or password. Please try again.")
                console.log(err)
            }
        },
        goMainPage(){
            this.$router.replace("/#")
        }
    }
}
</script>
 
