<template>
  <div class="team" v-if="!isMobile()">
    <div id='teamheadingdiv'>
      <heading id='teamheading'>Team Homepage</heading>
    </div>
    <div id="teamButton">
      <button @click="register" id="registerbutton">REGISTER A PROJECT</button>
    </div>

    <p id='teamProjectCheck'> Project List</p>
    <div id="teamProjectCheckBox" >
      <p v-for="(project, i) in checkData" v-bind:key='i'>
      <span @click="getProjData(project)" @dblclick="showDescription(project)">{{project}}</span>
      </p>
    </div>

    <div id='mouseHoverDiv'>
      <p>{{projectDescription}}</p>
    </div>

    <button @click='deleteSelect()' id="deleteOnebutton">Delete selected project</button>
    <button @click='deleteAll()' id="deletebutton">Delete all the projects</button>
    

    <p id='teamProjectStatus'> Project Information of: {{currentSelected}}</p>
    <div id="teamProjectStatusBox">
      <div id='teamProjectStatusBoxCover'></div>
      <div class='teamGithubBox'>
        <div class="projectStatus" id="teamGithub">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" id='teamregisterimg'>
        </div>
        <div class='projectStatus' id='teamGithubStatus'>
          <p class='projectTitle'>RePo Name:</p>
          <p class='projectBody'>{{githubRepoName}}</p>
          <p class='projectTitle'>Repo URL:</p>
          <p class='projectBody'> {{githubRepoURL}}</p>
          <p class='projectTitle'>Username:</p>
          <p class='projectBody'> {{githubUsername}}</p>
          <p class='projectTitle'>Authentication Token:</p>
          <p class='projectBody'> {{githubAuthenticationToken}}</p>
          <p class='projectTitle'>Project Name Registered in Acc Monitor:</p>
          <p class='projectBody'> {{githubProjectinAccMonitor}}</p>
        </div>
      </div>
      
      <div class='teamIncidentBox'>
        <div class="projectStatus" id="teamIncident">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" id='teamincidentimg' width=225 height=225>
        </div>
        <div class='projectStatus' id='teamIncidentStatus'>
          <p class='projectTitle'> Options:</p>
          <p class='projectBody'> {{incidentOption}} </p>
        </div>
      </div>
      
      <div class='teamJenkinsBox'>
        <div class="projectStatus" id="teamJenkins">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" id='teamrollbackimg' width=290 height=225>
        </div>
        <div class='projectStatus' id='teamJenkinsStatus'>
          <p class='projectTitle'>Server URL:</p>
          <p class='projectBody'> {{jenkinsServerURL}}</p>
          <p class='projectTitle'>Job Name:</p>
          <p class='projectBody'> {{jenkinsJobName}}</p>
          <p class='projectTitle'>Jenkins Username:</p>
          <p class='projectBody'> {{jenkinsUsername}}</p>
          <p class='projectTitle'>Authentication Token:</p>
          <p class='projectBody'> {{jenkinsAuthenticationToken}}</p>
          <p class='projectTitle'>Environment Associated with Pipeline:</p>
          <p class='projectBody'> {{jenkinsEAwP}}</p>
          <p class='projectTitle'>Project Name Registered in Acc Monitor:</p>
          <p class='projectBody'> {{jenkinsProjectinAccMonitor}}</p>
        </div>
      </div>
      
    </div>
    <p id='teamGotoDetail' @click='goStatusDetail()'> Show Details > </p>
  </div>
  
  <!-- Mobile User Interface -->
  <div id='teamMobile' v-else>
    <div id='teamheadingdivMobile'>
      <h1 id='teamheadingMobile'>Team Homepage</h1>
    </div>
    <p id='teamProjectCheckMobile'> Project List</p>
    <div id="teamProjectCheckBoxMobile" >
      <p v-for="(project, i) in projectList" v-bind:key='i'>
      <span @click="getInfo(project.id)">{{project.id}}</span>
      </p>
    </div>
    <button @click="register()" id="registerbuttonMobile">Register a Project</button>
    <button @click='deleteSelect()' id="deleteOnebuttonMobile">Delete selected</button>
    <button @click='deleteAll()' id="deletebuttonMobile">Delete all</button>

    <p id='teamProjectStatusMobile'> Project Information of: {{selectedID}}</p>
    <div id="teamProjectStatusBoxMobile">
      <div id='teamProjectStatusBoxCover'></div>
      <div class='teamGithubBox'>
        <div class="projectStatus" id="teamGithub">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" id='teamregisterimg'>
        </div>
        <div class='projectStatus' id='teamGithubStatus'>
          <p class='projectTitle'>RePo Name:</p>
          <p class='projectBody'>{{githubRepoName}}</p>
          <p class='projectTitle'>Repo URL:</p>
          <p class='projectBody'> {{githubRepoURL}}</p>
          <p class='projectTitle'>Username:</p>
          <p class='projectBody'> {{githubUsername}}</p>
          <p class='projectTitle'>Authentication Token:</p>
          <p class='projectBody'> {{githubAuthenticationToken}}</p>
          <p class='projectTitle'>Project Name Registered in Acc Monitor:</p>
          <p class='projectBody'> {{githubProjectinAccMonitor}}</p>
        </div>
      </div>

      <div class='teamSonarqubeBox'>
        <div class="projectStatus" id="teamSonarqube">
        <h2 class="title">SonarQube</h2>
        <img src = "../assets/deployment.png" id='teamdeploymentimg' width=225 height=225>
        </div>
        <div class='projectStatus' id='teamSonarqubeStatus'>
          <p class='projectTitle'>Instance Name:</p>
          <p class='projectBody'>{{sonarqubeInstanceName}} </p>
          <p class='projectTitle'>Project URL:</p>
          <p class='projectBody'> {{sonarqubeProjectURL}}</p>
          <p class='projectTitle'>Authentication Token:</p>
          <p class='projectBody'>{{sonarqubeAuthenticationToken}} </p>
        </div>
      </div>
      
      <div class='teamIncidentBox'>
        <div class="projectStatus" id="teamIncident">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" id='teamincidentimg' width=225 height=225>
        </div>
        <div class='projectStatus' id='teamIncidentStatus'>
          <p class='projectTitle'> Options:</p>
          <p class='projectBody'> {{incidentOption}} </p>
        </div>
      </div>
      
      <div class='teamJenkinsBox'>
        <div class="projectStatus" id="teamJenkins">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" id='teamrollbackimg' width=290 height=225>
        </div>
        <div class='projectStatus' id='teamJenkinsStatus'>
          <p class='projectTitle'>Server URL:</p>
          <p class='projectBody'> {{jenkinsServerURL}}</p>
          <p class='projectTitle'>Job Name:</p>
          <p class='projectBody'> {{jenkinsJobName}}</p>
          <p class='projectTitle'>Jenkins Username:</p>
          <p class='projectBody'> {{jenkinsUsername}}</p>
          <p class='projectTitle'>Authentication Token:</p>
          <p class='projectBody'> {{jenkinsAuthenticationToken}}</p>
          <p class='projectTitle'>Environment Associated with Pipeline:</p>
          <p class='projectBody'> {{jenkinsEAwP}}</p>
          <p class='projectTitle'>Project Name Registered in Acc Monitor:</p>
          <p class='projectBody'> {{jenkinsProjectinAccMonitor}}</p>
        </div>
      </div>
      
    </div>
    <p id='teamGotoDetailMobile' @click='goStatusDetail()'> Show Details > </p>
  </div>
</template>

<script>
import * as firebase from "firebase/app"
import "firebase/auth"
import axios from "axios"

export default{
  name: 'Team',
  data(){
    return{
      checkData: [],
      currentSelected:null,
      githubPart:null,
      sonarqubePart:null,
      incidentPart:null,
      jenkinsPart:null,
      projectName:null,
      githubRepoName:null,
      githubRepoURL:null,
      githubUsername:null,
      githubAuthenticationToken:null,
      githubProjectinAccMonitor:null,
      sonarqubeInstanceName:null,
      sonarqubeProjectURL:null,
      sonarqubeAuthenticationToken:null,
      jenkinsServerURL:null,
      jenkinsJobName:null,
      jenkinsUsername:null,
      jenkinsAuthenticationToken:null,
      jenkinsEAwP:null,
      jenkinsProjectinAccMonitor:null,
      incidentOption:null,
      projectList:[],
      projectInfo:{id:null, 
      githubRepoName:null,
      githubRepoURL:null,
      githubUsername:null,
      githubAuthenticationToken:null,
      githubProjectinAccMonitor:null,
      sonarqubeInstanceName:null,
      sonarqubeProjectURL:null,
      sonarqubeAuthenticationToken:null,
      jenkinsServerURL:null,
      jenkinsJobName:null,
      jenkinsUsername:null,
      jenkinsAuthenticationToken:null,
      jenkinsEAwP:null,
      jenkinsProjectinAccMonitor:null,
      incidentOption:null,
      projectDescription:null},
      selectedID:null,
      projectDescription:null,
    }
  },
  async created() {

      var memberURL = "http://35.9.22.64:8080/members/getByEmail/";
      var teamURL = "http://35.9.22.64:8080/teams/getTeamByName/";
      
      var userEmail = "";

      var user = firebase.auth().currentUser;
      console.log(user.email)

      if (user) {
        userEmail = user.email;   

        const response = await axios.get(memberURL + userEmail)

        for (var team in response.data.teams) {
          const teamData = await axios.get(teamURL + team)
          for (var project in teamData.data.projects) {
            this.checkData.push(project);
          }
        }

      }
      else {
        console.log("user not logged in")
      }
      
  },
  mounted() {
    if(localStorage.getItem('projectList')){
      this.projectList = JSON.parse(localStorage.getItem('projectList'));
    }
    if(!this.isExist(localStorage.getItem('projectName'))) {
      if (localStorage.getItem('projectName')) {
      try {
        this.projectName = JSON.parse(localStorage.getItem('projectName'));
        this.githubRepoName = JSON.parse(localStorage.getItem('githubRepoName'));
        this.githubRepoURL = JSON.parse(localStorage.getItem('githubRepoURL'));
        this.githubUsername = JSON.parse(localStorage.getItem('githubUsername'));
        this.githubAuthenticationToken = JSON.parse(localStorage.getItem('githubAuthenticationToken'));
        this.githubProjectinAccMonitor = JSON.parse(localStorage.getItem('githubProjectinAccMonitor'));
        this.sonarqubeInstanceName = JSON.parse(localStorage.getItem('sonarqubeInstanceName'));
        this.sonarqubeProjectURL = JSON.parse(localStorage.getItem('sonarqubeProjectURL'));
        this.sonarqubeAuthenticationToken = JSON.parse(localStorage.getItem('sonarqubeAuthenticationToken'));
        this.jenkinsServerURL = JSON.parse(localStorage.getItem('jenkinsServerURL'));
        this.jenkinsJobName = JSON.parse(localStorage.getItem('jenkinsJobName'));
        this.jenkinsUsername = JSON.parse(localStorage.getItem('jenkinsUsername'));
        this.jenkinsAuthenticationToken = JSON.parse(localStorage.getItem('jenkinsAuthenticationToken'));
        this.jenkinsEAwP = JSON.parse(localStorage.getItem('jenkinsEAwP'));
        this.jenkinsProjectinAccMonitor = JSON.parse(localStorage.getItem('jenkinsProjectinAccMonitor'));
        this.incidentOption = JSON.parse(localStorage.getItem('incidentOption'))
        this.projectDescription = JSON.parse(localStorage.getItem('projectDescription'))

        this.projectInfo.id = this.projectName;
        this.projectInfo.githubRepoName = this.githubRepoName;
        this.projectInfo.githubRepoURL = this.githubRepoURL;
        this.projectInfo.githubUsername = this.githubUsername;
        this.projectInfo.githubAuthenticationToken = this.githubAuthenticationToken;
        this.projectInfo.githubProjectinAccMonitor = this.githubProjectinAccMonitor;
        this.projectInfo.sonarqubeInstanceName = this.sonarqubeInstanceName;
        this.projectInfo.sonarqubeProjectURL = this.sonarqubeProjectURL;
        this.projectInfo.sonarqubeAuthenticationToken = this.sonarqubeAuthenticationToken;
        this.projectInfo.jenkinsServerURL = this.jenkinsServerURL;
        this.projectInfo.jenkinsJobName = this.jenkinsJobName;
        this.projectInfo.jenkinsUsername = this.jenkinsUsername;
        this.projectInfo.jenkinsAuthenticationToken = this.jenkinsAuthenticationToken;
        this.projectInfo.jenkinsEAwP = this.jenkinsEAwP;
        this.projectInfo.jenkinsProjectinAccMonitor = this.jenkinsProjectinAccMonitor;
        this.projectInfo.incidentOption = this.incidentOption;
        this.projectInfo.projectDescription = this.projectDescription;

        this.projectList.push(this.projectInfo);
        const parsedprojectList = JSON.stringify(this.projectList);
        localStorage.setItem('projectList', parsedprojectList);
        
        localStorage.removeItem('projectName')
      } catch(e) {
        alert(e);
        }
      }
    }
  },
  methods: {
        /*
        Remove All the data from localStorage except project list itself
         */
        async getProjData(project) {
          const parsedProjName = JSON.stringify(project);
          localStorage.setItem('projName', parsedProjName);

          this.currentSelected = project;

          if(document.getElementById("teamGithub").style.display=='none'){
                document.getElementById("teamGithub").style.display='block'
                document.getElementById("teamGithubStatus").style.display='none'
          } else {
                document.getElementById("teamGithub").style.display='none'
                document.getElementById("teamGithubStatus").style.display='block'
          }
          
          if(document.getElementById("teamJenkins").style.display=='none'){
                document.getElementById("teamJenkins").style.display='block'
                document.getElementById("teamJenkinsStatus").style.display='none'
          } 
          else {
            document.getElementById("teamJenkins").style.display='none'
            document.getElementById("teamJenkinsStatus").style.display='block'
          }
          if(document.getElementById("teamIncident").style.display=='none'){
            document.getElementById("teamIncident").style.display='block'
            document.getElementById("teamIncidentStatus").style.display='none'
          } 
          else {
            document.getElementById("teamIncident").style.display='none'
            document.getElementById("teamIncidentStatus").style.display='block'
          }

          this.githubRepoName = null;
          this.githubRepoURL = null;
          this.githubUsername = null;
          this.githubAuthenticationToken = null;
          this.githubProjectinAccMonitor = null;

          // get url
          var gitURL = "http://35.9.22.64:8080/products/getGithubProduct/"
          gitURL = gitURL + project

          var jenkinsURL = "http://35.9.22.64:8080/products/getJenkinsProduct/"
          jenkinsURL = jenkinsURL + project

          await axios.get(gitURL)
            .then(response => {
                    this.githubRepoName = response.data.name;
                    this.githubRepoURL = response.data.url;
                    this.githubUsername = response.data.userName;
                    this.githubAuthenticationToken = response.data.token;
                    this.githubProjectinAccMonitor = response.data.projectName;
                    
            })   
            
          await axios.get(jenkinsURL)
            .then(response => {
              this.jenkinsServerURL = response.data.url;
              this.jenkinsJobName = response.data.name;
              this.jenkinsUsername = response.data.userName;
              this.jenkinsAuthenticationToken = response.data.token;
              this.jenkinsEAwP = response.data.env;
              this.jenkinsProjectinAccMonitor = response.data.projectName;
            })

        },
        removeCurrentData(){
            localStorage.removeItem('githubRepoName');
            localStorage.removeItem('githubRepoURL');
            localStorage.removeItem('githubUsername');
            localStorage.removeItem('githubAuthenticationToken');
            localStorage.removeItem('githubProjectinAccMonitor');
            localStorage.removeItem('sonarqubeInstanceName');
            localStorage.removeItem('sonarqubeProjectURL');
            localStorage.removeItem('sonarqubeAuthenticationToken');
            localStorage.removeItem('jenkinsServerURL');
            localStorage.removeItem('jenkinsJobName');
            localStorage.removeItem('jenkinsUsername');
            localStorage.removeItem('jenkinsAuthenticationToken');
            localStorage.removeItem('jenkinsEAwP');
            localStorage.removeItem('jenkinsProjectinAccMonitor');
            localStorage.removeItem('incidentOption');
            localStorage.removeItem('githubStatus');
            localStorage.removeItem('incidentStatus');
            localStorage.removeItem('jenkinsStatus');
            localStorage.removeItem('sonarqubeStatus');
            localStorage.removeItem('projectDescription')
        },
        /*
        save what we have about the current projectList
        const parsedprojectList = JSON.stringify(this.projectList);
        localStorage.setItem('projectList', parsedprojectList);
         */
        register(){
          this.removeCurrentData()
          this.$router.push('Project')
        },
        /*
          I don't know if it is because the data is from json format
          there is a pair of " " outside the data
          so, I'm going to remove them before compare
          otherwise, there will be a problem
         */
        isExist(id){
          var result = false;
          for(var i=0; i<this.projectList.length; i++){
            var inputId = this.projectList[i]['id'];
            inputId = inputId.replace("\"","").replace("\"","");
            id = id.replace("\"","").replace("\"","");
            if(inputId === id){
              result = true;
            }
          }
          return result;
        },
        /*
        Remove All the data from localStorage
         */
        async deleteAll(){
          var r = confirm("You will loss all the projects, are you sure?")
          if (r==true){
            localStorage.removeItem('projectList');
            this.removeCurrentData();
            this.projectList = null;

            for (var i = 0; i < this.checkData.length; i++) {
              var proj = this.checkData[i];

              const projData = await axios.get("http://35.9.22.64:8080/projects/getProjectByName/" + proj)
              var id = projData.data.id;
              var team = projData.data.team;

              await axios.delete("http://35.9.22.64:8080/projects/deleteProject/" + id);

              const teamData = await axios.get("http://35.9.22.64:8080/teams/getTeamByName/" + team);
              var teamID = teamData.data.id;
              
              const delProj = {
                project: proj
              }

              await axios.put("http://35.9.22.64:8080/teams/removeProject/" + teamID, delProj)

              const index = this.checkData.indexOf(this.proj);
              if (index > -1) {
                this.checkData.splice(index, 1);
              }

            }

          }
        },
        /*
        Remove the data that the user selected
         */
        async deleteSelect(){
          var r = confirm("You will lose the selected project, are you sure?")
          if(r==true){
            const projData = await axios.get("http://35.9.22.64:8080/projects/getProjectByName/" + this.currentSelected)
            var id = projData.data.id;
            var team = projData.data.team;

            await axios.delete("http://35.9.22.64:8080/projects/deleteProject/" + id);

            const teamData = await axios.get("http://35.9.22.64:8080/teams/getTeamByName/" + team);
            var teamID = teamData.data.id;
            
            const delProj = {
              project: this.currentSelected
            }

            await axios.put("http://35.9.22.64:8080/teams/removeProject/" + teamID, delProj)

            const index = this.checkData.indexOf(this.currentSelected);
            if (index > -1) {
              this.checkData.splice(index, 1);
            }


          } 
        },
        /*
        Main part to show the information
        When users click on the title of project, its information will be displayed to users
         */
        getInfo(id){   
          for(var i=0; i<this.projectList.length;i++){
            var inputId = this.projectList[i]['id'];
            inputId = inputId.replace("\"","").replace("\"","");
            id = id.replace("\"","").replace("\"","");
            if(inputId == id){
              var githubRepoName = this.githubRepoName;
              this.githubRepoName = this.projectList[i]['githubRepoName'];
              var githubRepoURL = this.githubRepoURL;
              this.githubRepoURL = this.projectList[i]['githubRepoURL'];
              var githubUsername = this.githubUsername;
              this.githubUsername = this.projectList[i]['githubUsername'];
              var githubAuthenticationToken = this.githubAuthenticationToken;
              this.githubAuthenticationToken = this.projectList[i]['githubAuthenticationToken'];
              var githubProjectinAccMonitor = this.githubProjectinAccMonitor;
              this.githubProjectinAccMonitor = this.projectList[i]['githubProjectinAccMonitor'];
              this.githubPart = githubRepoName + "\n"+ githubRepoURL+"\n"+githubUsername+githubAuthenticationToken+githubProjectinAccMonitor
              var sonarqubeInstanceName = this.sonarqubeInstanceName;
              this.sonarqubeInstanceName = this.projectList[i]['sonarqubeInstanceName'];
              var sonarqubeProjectURL = this.sonarqubeProjectURL;
              this.sonarqubeProjectURL = this.projectList[i]['sonarqubeProjectURL'];
              var sonarqubeAuthenticationToken = this.sonarqubeAuthenticationToken;
              this.sonarqubeAuthenticationToken = this.projectList[i]['sonarqubeAuthenticationToken'];
              this.sonarqubePart = sonarqubeInstanceName + "\n"+sonarqubeProjectURL+ "\n"+sonarqubeAuthenticationToken
              var jenkinsServerURL = this.jenkinsServerURL;
              this.jenkinsServerURL = this.projectList[i]['jenkinsServerURL'];
              var jenkinsJobName = this.jenkinsJobName;
              this.jenkinsJobName = this.projectList[i]['jenkinsJobName'];
              var jenkinsUsername = this.jenkinsUsername;
              this.jenkinsUsername = this.projectList[i]['jenkinsUsername'];
              var jenkinsAuthenticationToken = this.jenkinsAuthenticationToken;
              this.jenkinsAuthenticationToken = this.projectList[i]['jenkinsAuthenticationToken'];
              var jenkinsEAwP = this.jenkinsEAwP;
              this.jenkinsEAwP = this.projectList[i]['jenkinsEAwP'];
              var jenkinsProjectinAccMonitor = this.jenkinsProjectinAccMonitor;
              this.jenkinsProjectinAccMonitor = this.projectList[i]['jenkinsProjectinAccMonitor'];
              this.jenkinsPart = jenkinsServerURL+jenkinsJobName+jenkinsUsername+jenkinsAuthenticationToken+jenkinsEAwP+jenkinsProjectinAccMonitor;
              var incidentOption = this.incidentOption;
              this.incidentOption = this.projectList[i]['incidentOption'];
              this.incidentPart = incidentOption;
              this.currentSelected = id;
              this.selectedID = id;
              var projectDescription = this.projectList[i]['projectDescription']
              this.projectDescription = projectDescription
            }
          }
        },
        /*
        double click event
        Showing project description to users when thay double click on a title
         */
        showDescription(id){
          for(var i=0; i<this.projectList.length;i++){
            var inputId = this.projectList[i]['id'];
            inputId = inputId.replace("\"","").replace("\"","");
            id = id.replace("\"","").replace("\"","");
            if(inputId == id){
              var projectDescription = this.projectList[i]['projectDescription']
              this.projectDescription = projectDescription
              alert("Project Description: " + projectDescription)
            }
          }
        },
        /*
        check users' device
        return true if they are using mobile devices
        return false if they are using devices other than mobile
         */
        isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
          }
        },
        /*
        go to the Status Detail page.
         */
        goStatusDetail(){
          if (this.isMobile() == true){
            alert('Viewing statistical charts on the computer will bring you a better experience.')
          }
          this.$router.push('StatusDetail')
        }
    }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'fordngbs-antenna-light',arial,helvetica,sans-serif;
  font-size: 16px;
}

span{
  margin:auto auto auto 10px;
  cursor: pointer;
}

#teamheadingdiv{
  min-width: 400px;
  top:15%;
  left:35%;
  width:30%;
  position:absolute;
  text-align: center;
  margin-bottom:50px;
}
#teamheading{
  font-size:42px;
  font-family: 'Courier New', Courier, monospace;
}

#deleteOnebutton{
  position: absolute;
  top:70%;
  left:5%;
  width: 25%;
  height:5%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#deleteOnebutton:hover{
  box-shadow: 0 30px 30px #5c5c5c;
  top:69.5%;
  left:3.75%;
  width: 27.5%;
  height:5.5%;
  font-size:18px;
  transition: 0.2s
}
#deletebutton{
  position: absolute;
  top:76%;
  left:5%;
  width: 25%;
  height:5%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#deletebutton:hover{
  box-shadow: 0 30px 30px #5c5c5c;
  top:75.5%;
  left:3.75%;
  width: 27.5%;
  height:5.5%;
  font-size:18px;
  transition: 0.2s
}
#registerbutton{
  min-width: 400px;
  top:22%;
  left:35%;
  width:30%;
  height:8%;
  position: absolute;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#registerbutton:hover{
  box-shadow: 0 30px 30px #5c5c5c;
  top: 21.2%;
  left: 32%;
  width:36%;
  height:9.6%;
  font-size:18px;
  transition: 0.2s
}

.projectStatus{
  border: 1px solid #cccccc;
  width: 330px;
  height: 330px;
