<template>
    <div id="commonheader">
    <nav>
        <div class="image" @click="goMainPage()">
            <img src="../assets/smallf.png" width="100vw">
        </div>
        <div class="menu-item"><a @click="goTeam()">Team</a></div>
        <!-- I don't know if we will need it in the future, for now, I will keep it here
        <div class="menu-item"><a href="/Project">Project</a></div>
        -->
        <!-- @click="goLogin()" -->
        <div class="rmenu-item" v-if="!loggedIn">
            <Dropdown id='dropdownTitle' title="Sign in" :items="loginIconOp" />
        </div>
       
        <div class="rmenu-item" v-if="loggedIn" @click="signOut()">
            <img src="../assets/icon.png" width="40vw">
            <p>{{userEmail}}</p>
        </div>
    </nav>
    </div>
</template>

<script>
import Dropdown from './Dropdown.vue'
import * as firebase from "firebase/app"
import "firebase/auth"

export default {
    name: 'navbar',
    components: {
        Dropdown
    },
    data () {
        return {
            loginIconOp: [{
                title: 'Team Info',
                link: '/Team'
            },
            {
                title: 'Products',
                link: '/Project'
            },
            {
                title: 'Login',
                link: '/Login'
            }
            ],
            loggedIn: false,
            userEmail: ''
        }
    },
    methods: {
        goLogin() {
            this.$router.push('Login')
        },
        goMainPage(){
            this.$router.replace("/#")
        },
        goTeam() {
            if (!this.loggedIn)
                this.$router.push('Login')
            else
                this.$router.push("/Team");
        },
        async signOut() {
            try {
                const data = await firebase.auth().signOut();
                console.log(data);
                this.$router.replace("Login")
            }catch(err) {
                console.log(err)
            }
        }
    },
    created() {
        firebase.auth().onAuthStateChanged(user=> {
            if (user) {
                this.loggedIn = true;
                this.userEmail = user.email
                
            } else {
                this.loggedIn = false;
            }
        })
    }
}
</script>

