<template>
  <div class="registeringProducts" v-if="!isMobile()">
      <div id='registeringProductsHeaderDiv'>
        <p id="registeringProductsHeader"> {{projectName}}</p>
      </div>
      <div class="registering" id='goregister' @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="incident" id='goincident' @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollback" id='gorollback' @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>

      <div id="finishedReturn">
        <button @click="goTeam" id='finishbutton'>Finish</button>
      </div>
  </div>
  <!-- Mobile User Interface -->
  <div class='registeringProductsMobile' v-else>
    <div id='registeringProductsHeaderDivMobile'>
        <p id="registeringProductsHeaderMobile"> {{projectName}}</p>
      </div>
      <div class="registeringMobile" id='goregister' @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="incidentMobile" id='goincident' @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollbackMobile" id='gorollback' @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>

      <div id="finishedReturnMobile">
        <button @click="goTeam" id='finishbuttonMobile'>Finish</button>
      </div>
  </div>
</template>

<script>
export default{
  name:"RegisteringProducts",
  data(){
    return{
      projectName:null,
      githubStatus:null,
      incidentStatus:null,
      jenkinsStatus:null,
      githubRepoName:null,
      githubRepoURL:null,
      githubUsername:null,
      githubAuthenticationToken:null,
      githubProjectinAccMonitor:null,
      jenkinsServerURL:null,
      jenkinsJobName:null,
      jenkinsUsername:null,
      jenkinsAuthenticationToken:null,
      jenkinsEAwP:null,
      jenkinsProjectinAccMonitor:null,
      incidentOption:null,
    }
  },
  created(){
  }, 
  mounted() {
    this.githubStatus = JSON.parse(localStorage.getItem('githubStatus'));
    this.incidentStatus = JSON.parse(localStorage.getItem('incidentStatus'));
    this.jenkinsStatus = JSON.parse(localStorage.getItem('jenkinsStatus'));
    this.projectName = JSON.parse(localStorage.getItem('projectName'));
    this.githubRepoName = JSON.parse(localStorage.getItem('githubRepoName'));
    this.githubRepoURL = JSON.parse(localStorage.getItem('githubRepoURL'));
    this.githubUsername = JSON.parse(localStorage.getItem('githubUsername'));
    this.githubAuthenticationToken = JSON.parse(localStorage.getItem('githubAuthenticationToken'));
    this.githubProjectinAccMonitor = JSON.parse(localStorage.getItem('githubProjectinAccMonitor'));
    this.jenkinsServerURL = JSON.parse(localStorage.getItem('jenkinsServerURL'));
    this.jenkinsJobName = JSON.parse(localStorage.getItem('jenkinsJobName'));
    this.jenkinsUsername = JSON.parse(localStorage.getItem('jenkinsUsername'));
    this.jenkinsAuthenticationToken = JSON.parse(localStorage.getItem('jenkinsAuthenticationToken'));
    this.jenkinsEAwP = JSON.parse(localStorage.getItem('jenkinsEAwP'));
    this.jenkinsProjectinAccMonitor = JSON.parse(localStorage.getItem('jenkinsProjectinAccMonitor'));
  },
  methods:{
    /*
    go to Register page
     */
    goRegister() {
      this.$router.push('Register')
    },
    /*
    go to incident page
     */
    goIncident(){
      this.$router.push('Incident')
    },
    /*
    go to rollback page
     */
    goRollback(){
      this.$router.push('Rollback')
    },
    /*
    I think we can send data to backend from this function.
    since it is the last step of registering a project.
    but for now, it will just serve for local storage.
    I will figure out how to send data to backend soon.
     */
    goTeam(){
      this.$router.push('Team');
    },
    /*
    check users' device
    return true if they are using mobile devices
    return false if they are using devices other than mobile
     */
    isMobile() {
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true
      } else {
        return false
        }
    }
  },
}

</script>


<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  font-family: 'fordngbs-antenna-light',arial,helvetica,sans-serif;
  font-size: 23px;
  text-align: center;
}

