  width:30%;
  height:8%;
  top:90%;
  left:35%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#finishbutton:hover{
  box-shadow: 0 30px 30px #5c5c5c;
  top: 89.4%;
  left: 32%;
  width:36%;
  height:9.6%;
