  transition: 0.2s
}



.registering,.incident,.rollback{
  position: absolute;
  text-align: center;
  background-color:white;
  width:25%;
  height:30%;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
@keyframes mainpageOptionsAnimation{
  0%{
    background:#f3f3f3;
    border: 3px solid #cccccc;
  }
  100%{
    background:lightblue;
    border: 3px solid #0276b3;
  }
}
.registering{
  left: 20%;
  top: 22%;
}
.registering:hover{
  animation: registerDeploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:17.5%;
  top:19%;
  font-size:18px;
  transition: 0.2s
}

.incident {
  left: 60%;
  top:22%;
}
.incident:hover{
  animation: registerDeploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:57.5%;
  top:19%;
  font-size:18px;
  transition: 0.2s
}

.rollback {
  left:20%;
  top:56%;
}
.rollback:hover{
  animation: registerDeploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:17.5%;
  top:53%;
  font-size:18px;
  transition: 0.2s
}

/* Mobile User Interface */
#registeringProductsHeaderDivMobile{
  top:15%;
  left:35%;
  width:30%;
  position:absolute;
  text-align: center;
  margin-bottom:50px;
}
#registeringProductsHeaderMobile {
  font-size:42px;
  font-family: 'Courier New', Courier, monospace;
}

#finishbuttonMobile{
  text-align: center;
  position: absolute;
  width:70%;
  height:8%;
  top:85%;
  left:12.5%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
.registeringProductsMobile img{
  width:60%;
  height:60%;
}
.registeringMobile,.incidentMobile,.rollbackMobile{
  position: absolute;
  text-align: center;
  background-color:white;
  width:36%;
  height:22%;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}

.registeringMobile{
  left: 15%;
