<template>
  <div class="incidents" v-if="!isMobile()">
    <div class='componentsHeadingDiv'>
      <p class='componentsHeading'>Incident Handling</p>
    </div>
    <div class='componentsDiscriptionDiv'>
      <p class='componentsDiscription'>Choose incident tracking method:</p>
    </div>

    <div id="incidentOptions">
      <li @click='addOption1()' id='Option1'>Send back end incident notification</li>
      <li @click='addOption2()' id='Option2'>Have back end create incidents by checking Spring health actuator endpoint</li>
    </div>
    <p id='incidentChoseOption'> {{noChoiceYet}} {{yourSelected}}</p>
  <div id="save">
    <button @click="saveIncidnetOptions" class='componentsSaveButton'>Save</button>
    <button @click='returnToRegisteringProducts' class='componentsReturnButton'>Return</button>
  </div>

    <div class="otherOptions">
      <div class="register" id="incidentRegister" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="rollback" id="incidentRollback" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>
  </div>
  <!-- Mobile User Interface -->
  <div class="incidentsMobile" id='incidentsMobile' v-else>
    <p id='IncidentHeadingMobile'>Incident Handling</p>
    <p id='discriptionMobile'>Choose incident tracking method:</p>
    <div id="incidentOptionsMobile">
      <li @click='addOption1()' id='Option1Mobile'>Send back end incident notification</li>
      <li @click='addOption2()' id='Option2Mobile'>Have back end create incidents by checking Spring health actuator endpoint</li>
    </div>
    <p id='incidentChoseOptionMobile'> {{noChoiceYet}} {{yourSelected}}</p>
  <div id="IncidentSaveMobile">
    <button @click="saveIncidnetOptions" id='incidentSaveButtonMobile'>Save</button>
    <button @click='returnToRegisteringProducts' id='incidentReturnButtonMobile'>Return</button>
  </div>

    <div class="otherOptionsMobile">
      <div class="register" id="incidentRegisterMobile" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="rollback" id="incidentRollbackMobile" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default{
  data(){
    return{
      incidentOption:null,
      githubStatus:null,
      incidentStatus:null,
      jenkinsStatus:null,
      noChoiceYet:null,
      yourSelected:null,
    }
  },
  mounted(){
    this.githubStatus = JSON.parse(localStorage.getItem('githubStatus'));
    this.incidentStatus = JSON.parse(localStorage.getItem('incidentStatus'));
    this.jenkinsStatus = JSON.parse(localStorage.getItem('jenkinsStatus'));
    if(this.yourSelected == null){
      this.noChoiceYet = 'You havn\'t made your choice yet, Please don\'t forget to save'
    }
  },
  methods: {
        /*
        go to register page
         */
        goRegister() {
            this.$router.push('Register')
        },
        /*
        go to incident page
         */
        goIncident(){
            this.$router.push('Incident')
        },
        /*
        go to rollback page
         */
        goRollback(){
            this.$router.push('Rollback')
        },
        /*
        save the change users made
        If users haven't make a choice, Web will alert them to make a choice.
         */
        saveIncidnetOptions(){
          if(!this.incidentOption){
          alert("Please make your choice");
            return; //ignore the save, you will have change to fill out the column
          }
          this.noChoiceYet = 'You chose the '
          if(this.incidentOption == 'Send back end incident notification'){
            this.yourSelected ='First option';
          } else{
            this.yourSelected='Second option'
          }
          const parsedIncidentOption = JSON.stringify(this.incidentOption);
          localStorage.setItem('incidentOption', parsedIncidentOption);
          const parsedincidentStatus = JSON.stringify("Complete!");
          localStorage.setItem('incidentStatus', parsedincidentStatus);
        },
        /*
        record option1: send back end incident notification
         */
        addOption1(){
          this.incidentOption = 'Send back end incident notification'
        },
        /*
        record option2: Have back end create incidents by checking Spring health actuator endpoint
        It will also ask users to enter Application URL
        Record both option2 and Application URL
         */
        addOption2(){
          var applicationURL = prompt('Please enter Application URL')
          this.incidentOption = 'Have back end create incidents by checking Spring health actuator endpoint' + '\n' + 'Application URL: ' + applicationURL;
        },
        /*
        go back to Registering Products page
         */
        returnToRegisteringProducts(){
          this.$router.push('RegisteringProducts')
        },
        /*
        check users' device
        return true if they are using mobile devices
        return false if they are using devices other than mobile
         */
        isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
          }
        }
    }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
  font-family: 'fordngbs-antenna-light',arial,helvetica,sans-serif;
  font-size: 16px;
}

#incidentOptions{
  font-size:23px;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  position:absolute;
  width:80%;
  left:10%;
  top:25%;
}

#Option1{
  width:100%;
  text-align: left;
  margin-bottom:50px;
  cursor:pointer;
}
#Option2{
  width:100%;
  text-align: left;
  cursor:pointer;
}

.centerbt li{
  content:".";
  color:black;
}

#Option1:hover{
  animation: incidentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
}
#Option2:hover{
  animation: incidentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
}

#incidentChoseOption{
  position:absolute;
  top:50%;
  left:10%;
  font-family: 'Courier New', Courier, monospace;
}
@keyframes incidentAnimation{
  0%{
    background:#f3f3f3;
    border: 3px solid #cccccc;
  }
  100%{
    background:lightblue;
    border: 3px solid #0276b3;
  }
}
#incidentRegister{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 20%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#incidentRegister:hover{
  animation: registerAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:27.5%;
  height:33%;
  left:18.75%;
  top:63.5%;
  font-size:18px;
  transition: 0.2s;
}

#incidentRollback{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 60%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#incidentRollback:hover{
  animation: registerAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:27.5%;
  height:33%;
  left:58.75%;
  top:63%;
  font-size:18px;
  transition: 0.2s;
}

/* Mobile User Interface */
.incidentsMobile p{
  font-size:16px;
}

#incidentsMobile{
  position: relative;
  text-align: center;
  margin-top:125px;
  margin-bottom:15px;
}


#IncidentHeadingMobile{
  font-size:26px;
  margin-bottom:25px;
}


#incidentOptionsMobile{
  margin-bottom:30px;
  text-align: left;
  margin-left:20px;
}
#Option1Mobile{
  margin-bottom:10px;
}
#Option2Mobile{
  margin-bottom:10px;
}

#incidentChoseOptionMobile{
  margin-top:20px;
  margin-bottom:20px;
}

#discriptionMobile{
  font-size:16px;
  margin-bottom:25px;
}

#incidentSaveMobile{
  width:100%;
  height:10%;
}
#incidentSaveButtonMobile{
  position: absolute;
  top:105%;
  left:15%;
  width: 35%;
  height:20%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 20px #5c5c5c;
}
#incidentReturnButtonMobile{
  position: absolute;
  top:105%;
  left:55%;
  width: 35%;
  height:20%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 20px #5c5c5c;
}

#incidentsMobile img{
  width:45%;
  height:45%;
  object-fit: contain;
}

#incidentRollbackMobile,#incidentRegisterMobile{
  width:30%;
  height:50%;
  background-color:white;
  border: 1px solid #cccccc;
  box-shadow:0px 10px 30px #5c5c5c;
}
#incidentRegisterMobile{
  position: absolute;
  left:15%;
  top:145%;
}

#incidentRollbackMobile{
  position: absolute;
  left:60%;
  top:145%;
}
</style>
