<template>
  <div class="rollbacks" v-if="!isMobile()">
    <div class='componentsHeadingDiv'>
      <p class='componentsHeading'>Jenkins Registration</p>
    </div>
    <div class='componentsDiscriptionDiv'>
      <p class='componentsDiscription'>To register the Jenkins instance used for your project, enter the following information:</p>
    </div>
    
  <div id="jenkinsInfo">
    <textarea name="textarea" placeholder="Server URL" style="resize:none" v-model='jenkinsServerURL' id='jenkinsServerURL'></textarea>
    <textarea name="textarea" placeholder="Job Name" style="resize:none" v-model='jenkinsJobName' id='jenkinsJobName'></textarea>
    <textarea name="textarea" placeholder="Jenkins Username" style="resize:none" v-model='jenkinsUsername' id='jenkinsUsername'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='jenkinsAuthenticationToken' id='jenkinsAuthenticationToken'></textarea>
    <textarea name="textarea" placeholder="Environment Associated with Pipeline" style="resize:none" v-model='jenkinsEAwP' id='jenkinsEAwP'></textarea>
  </div>
  <div id="save">
  <button @click="saveJenkins" class='componentsSaveButton'>Save</button>
  <button @click="registerJenkins()" class='componentsReturnButton'>Return</button>
  </div>
    <div class="otherOptions">
        <div class="register" id="rollbackRegister" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="rollback" id="rollbackIncident" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
    </div>
  </div>
  <!-- Mobile User Interface -->
  <div class="rollbacksMobile" id='rollbacksMobile' v-else>
    <p id='rollbackHeadingMobile'>Jenkins Registration</p>
    <p id='discriptionMobile'>To register the Jenkins instance used for your project, enter the following information:</p>
  <div id="jenkinsInfoMobile">
    <textarea name="textarea" placeholder="Server URL" style="resize:none" v-model='jenkinsServerURL' id='jenkinsServerURL'></textarea>
    <textarea name="textarea" placeholder="Job Name" style="resize:none" v-model='jenkinsJobName' id='jenkinsJobName'></textarea>
    <textarea name="textarea" placeholder="Jenkins Username" style="resize:none" v-model='jenkinsUsername' id='jenkinsUsername'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='jenkinsAuthenticationToken' id='jenkinsAuthenticationToken'></textarea>
    <textarea name="textarea" placeholder="Environment Associated with Pipeline" style="resize:none" v-model='jenkinsEAwP' id='jenkinsEAwP'></textarea>
  </div>
  <div id="rollbackSaveMobile">
  <button @click="saveJenkins" id='rollbackSaveButtonMobile'>Save</button>
  <button @click='returnToRegisteringProducts' id='rollbackReturnButtonMobile'>Return</button>
  </div>
    <div class="otherOptionsMobile">
        <div class="registerMobile" id="rollbackRegisterMobile" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="rollbackMobile" id="rollbackIncidentMobile" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default{
  data() {
    return{
      jenkinsServerURL:null,
      jenkinsJobName:null,
      jenkinsUsername:null,
      jenkinsAuthenticationToken:null,
      jenkinsEAwP:null,
      jenkinsProjectinAccMonitor:null,
      githubStatus:null,
      incidentStatus:null,
      jenkinsStatus:null,
      projectName: null
    }
  },
  mounted(){
    this.projectName = JSON.parse(localStorage.getItem('projectName'));
    this.githubStatus = JSON.parse(localStorage.getItem('githubStatus'));
    this.incidentStatus = JSON.parse(localStorage.getItem('incidentStatus'));
    this.jenkinsStatus = JSON.parse(localStorage.getItem('jenkinsStatus'));
  },
  methods: {
        isValidURL(string) {
          try {
            new URL(string);
          } catch (_) {
            return false;
          }

          return true;
        },
        /*
        go to register page
         */
        goRegister() {
            this.$router.push('Register')
        },
        /*
        go to incident page
         */
        goIncident(){
            this.$router.push('Incident')
        },
        /*
        go to rollback page
         */
        goRollback(){
            this.$router.push('Rollback')
        },
        /*
        record all input made by the user
        If there are some columns that users miss, web will alert them to finish all the column
         */
        saveJenkins(){
          if(!this.jenkinsServerURL || !this.jenkinsJobName || !this.jenkinsUsername || !this.jenkinsAuthenticationToken || !this.jenkinsEAwP){
            alert("Please finish all the column below");
            return; //ignore the save, you will have change to fill out the column
          }
          const parsedjenkinsServerURL = JSON.stringify(this.jenkinsServerURL);
          localStorage.setItem('jenkinsServerURL', parsedjenkinsServerURL);
          const parsedjenkinsJobName = JSON.stringify(this.jenkinsJobName);
          localStorage.setItem('jenkinsJobName', parsedjenkinsJobName);
          const parsedjenkinsUsername = JSON.stringify(this.jenkinsUsername);
          localStorage.setItem('jenkinsUsername', parsedjenkinsUsername);
          const parsedjenkinsAuthenticationToken = JSON.stringify(this.jenkinsAuthenticationToken);
          localStorage.setItem('jenkinsAuthenticationToken', parsedjenkinsAuthenticationToken);
          const parsedjenkinsEAwP = JSON.stringify(this.jenkinsEAwP);
          localStorage.setItem('jenkinsEAwP', parsedjenkinsEAwP);
          const parsedjenkinsProjectinAccMonitor = JSON.stringify(this.jenkinsProjectinAccMonitor);
          localStorage.setItem('jenkinsProjectinAccMonitor', parsedjenkinsProjectinAccMonitor);

          const parsedJenkinsStatus = JSON.stringify("Complete!");
          localStorage.setItem('jenkinsStatus', parsedJenkinsStatus);
        },
        /*
        go back to Registering Products page
         */
        registerJenkins(){
          if(!this.jenkinsJobName || !this.jenkinsProjectURL || !this.jenkinsAuthenticationToken){
            alert("Please finish all the column below");
            return; //ignore the save, you will have change to fill out the column
          }

          if (!this.isValidURL(this.jenkinsProjectURL)) { //check if URL is a valid URL
            alert("Please enter a valid URL");
            return;
          }

          const jenkinsData = {
            name: this.jenkinsJobName,
            url: this.jenkinsProjectURL,
            userName: this.jenkinsUsername,
            token: this.jenkinsAuthenticationToken,
            env: this.jenkinsEAwP,
            projectName: this.projectName
          }

          axios.post("http://35.9.22.64:8080/products/addJenkinsProduct", jenkinsData)
            .then(response => {
              console.log(response.data)
            })
            .catch(error => {
              console.log(error);
            })

          this.$router.push('RegisteringProducts')
        },
        /*
        check users' device
        return true if they are using mobile devices
        return false if they are using devices other than mobile
         */
        isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
          }
        }
    }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'fordngbs-antenna-light',arial,helvetica,sans-serif;
  font-size: 16px;
}

#heading{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  position:relative;
  top:50px;
  margin-top:50px;
  margin-bottom:75px;
  text-align: center;
}

#discription{
  font-size:23px;
  text-align: center;
}


#jenkinsInfo{
  position:absolute;
  text-align: center;
  width:100%;
  top:18%;
}
#jenkinsInfo textarea{
  width:40%;
}

#save{
  text-align: center;
}

@keyframes rollbackAnimation{
  0%{
    background:#f3f3f3;
    border: 3px solid #cccccc;
  }
  100%{
    background:lightblue;
    border: 3px solid #0276b3;
  }
}

#rollbackRegister{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 20%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#rollbackRegister:hover{
  animation: registerAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:27.5%;
  height:33%;
  left:18.75%;
  top:63.5%;
  font-size:18px;
  transition: 0.2s;
}

#rollbackIncident{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 60%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#rollbackIncident:hover{
  animation: registerAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:27.5%;
  height:33%;
  left:58.75%;
  top:63%;
  font-size:18px;
  transition: 0.2s;
}

/* Mobile User Interface */
.rollbacksMobile p{
  font-size:16px;
}

#rollbacksMobile{
  position: relative;
  text-align: center;
  margin-top:125px;
  margin-bottom:15px;
}


#rollbackHeadingMobile{
  font-size:26px;
  margin-bottom:25px;
}

#discriptionMobile{
  font-size:16px;
  margin-bottom:25px;
}
.headingMobile{
  font-size:26px;
}

#jenkinsInfoMobile{
  position: relative;
  margin-top:10px;
  width:300px;
  left:0%;
}
#jenkinsInfoMobile textarea{
  width:80%;
  font-size:20px;
}

#rollbackSaveMobile{
  width:100%;
  height:10%;
}
#rollbackSaveButtonMobile{
  position: absolute;
  top:105%;
  left:15%;
  width: 35%;
  height:12%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 20px #5c5c5c;
}
#rollbackReturnButtonMobile{
  position: absolute;
  top:105%;
  left:55%;
  width: 35%;
  height:12%;
  background-color: #102b4e;
  color: white;
  box-shadow:0px 10px 20px #5c5c5c;
}

#rollbacksMobile img{
  width:45%;
  height:45%;
  object-fit: contain;
}

#rollbackIncidentMobile,#rollbackRegisterMobile{
  width:30%;
  height:30%;
  background-color:white;
  border: 1px solid #cccccc;
  box-shadow:0px 10px 30px #5c5c5c;
}
#rollbackRegisterMobile{
  position: absolute;
  left:15%;
  top:125%;
}
#rollbackIncidentMobile{
  position: absolute;
  left:60%;
  top:125%;
}
</style>
