import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Login from '../views/Login.vue'
import Team from '../views/Team.vue'
import Project from '../views/Project.vue'
import Register from'../views/Register.vue'
import UserRegister from'../views/UserRegister.vue'
import Deployment from'../views/Deployment.vue'
import Incident from'../views/Incident.vue'
import Rollback from'../views/Rollback.vue'
import RegisteringProducts from '../views/RegisteringProducts'
import StatusDetail from '../views/StatusDetail.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Main',
    component: Main,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login,
    meta: {
      keepAlive: false // Navbar is not active in login page
    }
  },
  {
    path: '/UserRegister',
    name: 'UserRegister',
    component: UserRegister,
    meta: {
      keepAlive: false // Navbar is not active in user registration page
    }
  },
  {
    path: '/Team',
    name :'Team',
    component: Team,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/Project',
    name : 'Project',
    component: Project,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/Register',
    name : 'Register',
    component: Register,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/Deployment',
    name : 'Deployment',
    component: Deployment,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/Incident',
    name : 'Incident',
    component: Incident,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/Rollback',
    name : 'Rollback',
    component: Rollback,
    meta: {
      keepAlive: true
    }
  },
  {
    path:'/RegisteringProducts',
    name:'RegisteringProducts',
    component: RegisteringProducts,
    meta: {
      keepAlive:true
    }
  },
  {
    path:'/StatusDetail',
    name:'StatusDetail',
    component: StatusDetail,
    meta: {
      keepAlive:true
    }
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
