<template>
  <div id="statusDetail" v-if="!isMobile()">
      <div id='statusDetailHeadingDiv'>
          <p id='statusDetailHeading'>{{Status}}</p>
          <h2 v-if="!toggle">{{projName}}</h2>
          <h2 v-if="toggle">{{projName}} & {{secondProj}}</h2>
          <input id="inputTime" v-model="selectedMonth" :placeholder="selectedMonth" @keyup.enter="changeData(Status)">
          <input id="inputTime" v-model="selectedYear" :placeholder="selectedYear" @keyup.enter="changeData(Status)">
      </div>
      
    <div id='statusDetailLeft'>
    <div class="sideBar">
        <dl>
            
            <dd class="ftName consult" id='LeadTime' @click='displayLeadTime()'>
                <span>Lead Time</span>
            </dd>
            <dd class="ftName words" id='DeploymentFrequency' @click='displayDeploymentFrequency()'>
                <span>Deployment Frequency</span>
            </dd>
            <dd class="ftName quote" id='MTTR' @click='displayMTTR()'>
                <span>Mean Time to Restore (MTTR)</span>
            </dd>
            <dd class="ftName quote" id='ChangeFailPercentage' @click='displayChangeFailPercentage()'>
                <span>Change Fail Percentage</span>
            </dd>
        </dl>

        <dl>

            <dd class="ftName quotes" id='Compare' @click="toggleCompare()">
                <span>Compare Mode: {{compareStatus}}</span>
            </dd>    

            <div id="projectBox" v-if="toggle" display="block">
                <p id="projectName" v-for="(project, i) in projectList" v-bind:key='i' @click="changeProj(project)">{{project}}</p>
            </div>

        </dl>

    </div>
    </div>
    <div id='statusDetailRight'>

        

        <div id='statusDetailLeadTime'>
            <div id='leadTimeChartDiv'>
                <canvas id="leadTimeChart" width="1200" height="600" margin-top="100px"></canvas>
            </div>
            <div id='leadTimeInfoDiv'> <!--
                <p> Average Lead Time of the last seven days: </p>
                <p> Average Lead Time of the most recent month: </p> --->
                <p> Average Lead Time Overall for {{projName}}: {{leadTimeAverage}} hours</p>
            </div>

            <div v-if="toggle" id='leadTimeInfoDivTwo'> <!--
                <p> Average Lead Time of the last seven days: </p>
                <p> Average Lead Time of the most recent month: </p> --->
                <p> Average Lead Time Overall for {{secondProj}}: {{leadTimeAverageTwo}} hours</p>
            </div>

        </div>





        <div id='statusDetailDeploymentFrequency'>
            <div id='firstChartDiv'>
                <canvas id="firstChart" width="1200" height="300" margin-top="20px"></canvas>
            </div>
            <div id='secondChartDiv'>
                <canvas id="secondChart" width="1200" height="300" margin-top="20px"></canvas>
            </div>

        </div>





        <div id='statusDetailMTTR'>
            <div id='MTTRChartDiv'>
                <canvas id="MTTRChart" width="1200" height="600" margin-top="20px"></canvas>
            </div>
            <div id='MTTRInfoDiv'> <!--
                <p> Average MTTR of the last seven days: </p>
                <p> Average MTTR of the most recent month: </p> --->
                <p id="MTTRptag"> Average MTTR Overall for {{projName}}: {{MTTRChartAverage}} minutes</p>
            </div>

            <div v-if="toggle" id='MTTRInfoDivTwo'> <!--
                <p> Average MTTR of the last seven days: </p>
                <p> Average MTTR of the most recent month: </p> --->
                <p> Average MTTR Overall for {{secondProj}}: {{MTTRChartAverageTwo}} minutes</p>
            </div>

        </div>

        <div id='statusDetailChangeFailPercentage'>
            

            <div id='changeFailPercentageChartDiv'>
                <canvas id="changeFailPercentageChart" width="1200" height="600" margin-top="20px"></canvas>
            </div>
            <div id='changeFailPercentageInfoDiv'> <!--
                <p> Average Change Fail Percentage of the last seven days: </p>
                <p> Average Change Fail Percentage of the most recent month: </p> --->
                <p> Average Change Fail Percentage Overall of {{projName}}: {{changeFailPercentageChartAverage}}%</p>
            </div>

            <div v-if="toggle" id='changeFailPercentageInfoDivTwo'> <!--
                <p> Average Change Fail Percentage of the last seven days: </p>
                <p> Average Change Fail Percentage of the most recent month: </p> --->
                <p> Average Change Fail Percentage Overall of {{secondProj}}: {{changeFailPercentageChartAverageTwo}}%</p>
            </div>
        </div>

    </div>

  </div>
  <!-- Mobile User Interface -->
  <div id='statusDetailMobile' v-else>
      <div id='statusDetailHeadingDiv'>
          <p id='statusDetailHeading'> {{Status}}</p>
      </div>
      
    <div id='statusDetailLeftMobile'>
    <div class="sideBarMobile">
        <dl>
            <dd class="ftName consult" id='LeadTime' @click='displayLeadTime()'>
                <span>Lead Time</span>
            </dd>
            <dd class="ftName words" id='DeploymentFrequency' @click='displayDeploymentFrequency()'>
                <span>Deployment Frequency</span>
            </dd>
            <dd class="ftName quote" id='MTTR' @click='displayMTTR()'>
                <span>Mean Time to Restore (MTTR)</span>
            </dd>
            <dd class="ftName quote" id='ChangeFailPercentage' @click='displayChangeFailPercentage()'>
                <span>Change Fail Percentage</span>
            </dd>
        </dl>
    </div>
    </div>
    <div id='statusDetailRight'>

        <div id='statusDetailLeadTime'>
            <div id='leadTimeChartDiv'>
                <canvas id="leadTimeChart" width="600" height="400"></canvas>
            </div>
            <div id='leadTimeInfoDiv'> 
                <p> Average Lead Time of the last seven days: </p>
                <p> Average Lead Time of the most recent month: </p> 
                <p> Average Lead Time overall: </p>
            </div>
        </div>

        <div id='statusDetailDeploymentFrequency'>
            <div id='firstChartDivMobile'>
                <canvas id="firstChart" width="600" height="300"></canvas>
            </div>
            <div id='secondChartDivMobile'>
                <canvas id="secondChart" width="600" height="300"></canvas>
            </div>
        </div>

        <div id='statusDetailMTTR'>
            <div id='MTTRChartDiv'>
                <canvas id="MTTRChart" width="600" height="400"></canvas>
            </div>
            <div id='MTTRInfoDiv'> 
                <p> Average MTTR of the last seven days: </p>
                <p> Average MTTR of the most recent month: </p> 
                <p> Average MTTR overall: </p>
            </div>
        </div>

        <div id='statusDetailChangeFailPercentage'>
            <div id='changeFailPercentageChartDiv'>
                <canvas id="changeFailPercentageChart" width="600" height="400"></canvas>
            </div>
            <div id='changeFailPercentageInfoDiv'> 
                <p> Average Change Fail Percentage of the last seven days: </p>
                <p> Average Change Fail Percentage of the most recent month: </p> 
                <p> Average Change Fail Percentage overall: </p>
            </div>
        </div>
    </div>
  </div>

</template>

<script>
import axios from "axios"
import Chart from 'chart.js'
import * as ChartAnnotation from 'chartjs-plugin-annotation'
import * as firebase from "firebase/app"
import "firebase/auth"

Chart.plugins.register([ChartAnnotation])

export default {
    data() {
        return {
            projectList:[],
            secondProj:null,
            toggle:false,
            compareStatus:"Off",
            Status:null,
            firstChart:null,
            firstCharData:null,
            secondChart:null,
            leadTimeChartLabels:null,
            leadTimeChartData:null,
            leadTimeAverage:null,
            MTTRChartLabels:null,
            MTTRChartData:null,
            MTTRChartAverage:null,
            changeFailPercentageChartData:null,
            changeFailPercentageChartLabels:null,
            changeFailPercentageChartAverage:null,
            changeFailList: [],
            deployData: [],
            months: ["january", "february", "march", "april", "may", "june", "july", "august", "september", "october", "november", "december"],
            selectedMonth: "October",
            selectedYear: 2020,
            dataMonth: 9,
            dataYear: 2020,
            weekStarts: [],
            projName:null,
            mttrData:[],
            leadData: [],
            mttrDataTwo: [],
            MTTRChartLabelsTwo:null,
            MTTRChartDataTwo:null,
            MTTRChartAverageTwo:null,

            changeFailPercentageChartDataTwo:null,
            changeFailPercentageChartLabelsTwo:null,
            changeFailPercentageChartAverageTwo:null,
            changeFailListTwo: [],

            leadTimeChartLabelsTwo:null,
            leadTimeChartDataTwo:null,
            leadTimeAverageTwo:null,
            leadDataTwo:[],

            deployDataTwo: [],
            firstChartTwo:null,
            firstCharDataTwo:null,
            secondChartTwo:null,
        };
    },
    async created() {

        this.projName = JSON.parse(localStorage.getItem('projName'));
        var weeks = this.monthWeeks(this.dataYear, this.dataMonth);
        var initialDate = weeks[0];
        
        const cdata = {
            statRequested: "Change Fail Percentage",
            targetProject: this.projName,
            startDate: initialDate
        }
    

        for (var i = 0; i < weeks.length; i++){
            cdata.startDate = weeks[i]
            await axios.post("http://35.9.22.64:8080/records/display", cdata)
                .then(response => {
             
                    if (response.data != "NaN")
                        this.changeFailList.push(response.data[0])
                    else
                        this.changeFailList.push(0)
                })
        }
     
        /* Delpoyment Frequency */
        const deploy = {
            statRequested: "Deployment Frequency",
            targetProject: this.projName,
            startDate: initialDate
        }
    
        await axios.post("http://35.9.22.64:8080/records/display", deploy)
            .then(response => {

                for (let index = 0; index < response.data.length; index++) {
                    const elem = response.data[index];
                    this.deployData.push(elem);
                }
            })

        /* Mean Time To Restore Data */
        const mttr = {
            statRequested: "Mean Time To Restore",
            targetProject: this.projName,
            startDate: initialDate
        }
        for (var x = 0; x < weeks.length; x++){
            mttr.startDate = weeks[x]
            await axios.post("http://35.9.22.64:8080/records/display", mttr)
                .then(response => {

                    if (response.data != "NaN")
                        this.mttrData.push(response.data[0])
                    else
                        this.mttrData.push(0)
                })
        }
     
        
        /* Lead Time data */
        const leadTime = {
            statRequested: "Lead Time",
            targetProject: this.projName,
            startDate: initialDate
        }

        for (var a = 0; a < weeks.length; a++){
            leadTime.startDate = weeks[a]
            await axios.post("http://35.9.22.64:8080/records/display", leadTime)
                .then(response => {

                    if (response.data != "NaN")
                        this.leadData.push(response.data[0])
                    else
                        this.leadData.push(0)
                })
        }
    

        var memberURL = "http://35.9.22.64:8080/members/getByEmail/";
        var teamURL = "http://35.9.22.64:8080/teams/getTeamByName/";
        
        var userEmail = "";

        var user = firebase.auth().currentUser;

        if (user) {
            userEmail = user.email;   

            const response = await axios.get(memberURL + userEmail)

            for (var team in response.data.teams) {
                const teamData = await axios.get(teamURL + team)
                for (var project in teamData.data.projects) {
                    this.projectList.push(project);
                }
            }

            const index = this.projectList.indexOf(this.projName);
            if (index > -1) {
                this.projectList.splice(index, 1)
            }

            this.secondProj = this.projectList[0];

        }
        else {
            console.log("user not logged in")
        }

        this.giveData()
        this.giveLeadTimeData();
        this.giveDataToMTTR()
        this.giveDataToChangeFail()
        this.makeFirstChart()
        this.makeSecondChart()
        this.makeLeadChart();
        this.makeMTTRChart()
        this.makeChangeFailPercentageChart()
    },
methods:{
    changeProj(project) {
        this.secondProj = project;
    },
    toggleCompare() {
        if (this.compareStatus == "Off") {
            this.compareStatus = "On";
            this.toggle = !this.toggle;
            if (this.Status == "Change Fail Percentage") {
                if (this.changeFailPercentageChart != undefined) {
                    this.changeFailPercentageChart.destroy();
                }
                this.changeData(this.Status);
            }
            else if (this.Status == "Deployment Frequency") {
                if (this.firstChart != undefined) {
                    this.firstChart.destroy();
                    this.secondChart.destroy();
                }
                this.changeData(this.Status);
            }
            else if (this.Status == "Lead Time") {
                if (this.leadTimeChart != undefined) {
                    this.leadTimeChart.destroy();
                }
                this.changeData(this.Status);
            }
            else if (this.Status == "Mean Time to Restore") {
                if (this.MTTRChart != undefined) {
                    this.MTTRChart.destroy();
                }
                this.changeData(this.Status);
            }
            
        }
        else {
            this.compareStatus = "Off"
            this.toggle = !this.toggle;
            this.changeData(this.Status);
        }
    },
    async secondChangeData(Status) {
        var weeks = this.monthWeeks(this.dataYear, this.dataMonth);

        var initialDate = weeks[0];

        if (Status == "Deployment Frequency") {
            this.deployDataTwo = []

            const deploy = {
                statRequested: "Deployment Frequency",
                targetProject: this.secondProj,
                startDate: initialDate
            }
            // Deployment Frequency
            const { data } = await axios.post("http://35.9.22.64:8080/records/display", deploy)
            for (let index = 0; index < data.length; index++) {
                    const element = data[index];
                    this.deployDataTwo.push(element);
            }

            this.giveDataTwo();

        }
        else if (Status == "Mean Time to Restore") {
            this.mttrDataTwo = [];

            const mttr = {
                statRequested: "Mean Time To Restore",
                targetProject: this.secondProj,
                startDate: initialDate
            }

            for (var x = 0; x < weeks.length; x++){
                mttr.startDate = weeks[x]
                await axios.post("http://35.9.22.64:8080/records/display", mttr)
                    .then(response => {
                        if (response.data != "NaN")
                            this.mttrDataTwo.push(response.data[0])
                        else
                            this.mttrDataTwo.push(0)
                    })
            }

            this.giveDataToMTTRTwo();
            this.makeMTTRChart();

        }
        else if (Status == "Change Fail Percentage") {
            this.changeFailListTwo = []

            const cfp = {
                statRequested: "Change Fail Percentage",
                targetProject: this.secondProj,
                startDate: initialDate
            }

            for (var i = 0; i < weeks.length; i++){
                cfp.startDate = weeks[i]
                await axios.post("http://35.9.22.64:8080/records/display", cfp)
                    .then(response => {
                        if (response.data != "NaN")
                            this.changeFailListTwo.push(response.data[0])
                        else
                            this.changeFailListTwo.push(0)
                    })
            }

            this.giveDataToChangeFailTwo();
            this.makeChangeFailPercentageChart();

        }

        else if (Status == "Lead Time") {
            const leadTime = {
                statRequested: "Lead Time",
                targetProject: this.secondProj,
                startDate: initialDate
            }   

            for (var a = 0; a < weeks.length; a++){
                leadTime.startDate = weeks[a]
                await axios.post("http://35.9.22.64:8080/records/display", leadTime)
                    .then(response => {

                        if (response.data != "NaN")
                            this.leadDataTwo.push(response.data[0])
                        else
                            this.leadDataTwo.push(0)
                    })
            }
      

            this.giveLeadTimeDataTwo();
        }
    },
    async changeData(Status) {
        
        this.selectedMonth = this.selectedMonth.toLowerCase();

        if (!this.months.indexOf(this.selectedMonth)) {
            alert("Please enter a valid month/year.");
        }
        else {

            this.dataMonth = this.months.indexOf(this.selectedMonth)
            var weeks = this.monthWeeks(this.selectedYear, this.dataMonth);
            var initialDate = this.postDateFormatter(this.selectedYear, this.dataMonth, 1);

            /* Change Fail Percentage */

            if (this.changeFailPercentageChart != undefined) {
                    this.changeFailPercentageChart.destroy();
            }

            this.changeFailList = [];
            
            const cdata = {
                statRequested: "Change Fail Percentage",
                targetProject: this.projName,
                startDate: initialDate
            }
            for (var i = 0; i < weeks.length; i++){
                cdata.startDate = weeks[i]
                await axios.post("http://35.9.22.64:8080/records/display", cdata)
                    .then(response => {
                        if (response.data != "NaN")
                            this.changeFailList.push(response.data[0])
                        else
                            this.changeFailList.push(0)
                    })
            }
            this.changeFailPercentageChartData = this.changeFailList;
            this.giveDataToChangeFail();  

            if (this.toggle) {
                this.changeFailListTwo = []

                const cfp = {
                    statRequested: "Change Fail Percentage",
                    targetProject: this.secondProj,
                    startDate: initialDate
                }

                for (var a = 0; a < weeks.length; a++){
                    cfp.startDate = weeks[a]
                    await axios.post("http://35.9.22.64:8080/records/display", cfp)
                        .then(response => {
                            if (response.data != "NaN")
                                this.changeFailListTwo.push(response.data[0])
                            else
                                this.changeFailListTwo.push(0)
                        })
                }
                this.changeFailPercentageChartDataTwo = this.changeFailListTwo;
                this.giveDataToChangeFailTwo();

            }
            this.makeChangeFailPercentageChart();

            
            /* Deployment Frequency */

            if (this.firstChart != undefined) {
                    this.firstChart.destroy();
                    this.secondChart.destroy();
            }

            this.tempDeployData = [];
            this.deployData = [];
            this.firstCharData = [];

            const deploy = {
                statRequested: "Deployment Frequency",
                targetProject: this.projName,
                startDate: initialDate
            }

            await axios.post("http://35.9.22.64:8080/records/display", deploy)
                .then(response => {
                    for (let pos = 0; pos < response.data.length; pos++) {
                        const elem = response.data[pos];
                        this.deployData.push(elem);
                    }
                })

            this.tempDeployData = this.deployData;
            this.giveData();
            this.giveDataToSecondChart();
           

            if (this.toggle) {
                this.deployDataTwo = []

                const deploy = {
                    statRequested: "Deployment Frequency",
                    targetProject: this.secondProj,
                    startDate: initialDate
                }

                await axios.post("http://35.9.22.64:8080/records/display", deploy)
                    .then(response => {
                        for (let ind = 0; ind < response.data.length; ind++) {
                            const item = response.data[ind];
                            this.deployDataTwo.push(item);
                        }
                    })

                this.giveDataTwo();

            }

            this.makeFirstChart();
            this.makeSecondChart();
        
            
            /* Mean Time To Restore */

            if (this.MTTRChart != undefined) {
                    this.MTTRChart.destroy();
            }

            this.mttrData = [];

            const mttr = {
                statRequested: "Mean Time To Restore",
                targetProject: this.projName,
                startDate: initialDate
            }

            for (var j = 0; j < weeks.length; j++){
                mttr.startDate = weeks[j]
                await axios.post("http://35.9.22.64:8080/records/display", mttr)
                    .then(response => {
                        if (response.data != "NaN")
                            this.mttrData.push(response.data[0])
                        else
                            this.mttrData.push(0)
                    })
            }
            this.giveDataToMTTR();

            if (this.toggle) {
                this.mttrDataTwo = []

                const mttr = {
                    statRequested: "Mean Time To Restore",
                    targetProject: this.secondProj,
                    startDate: initialDate
                }

                for (var y = 0; y < weeks.length; y++){
                    mttr.startDate = weeks[y]
                    await axios.post("http://35.9.22.64:8080/records/display", mttr)
                        .then(response => {
                            if (response.data != "NaN")
                                this.mttrDataTwo.push(response.data[0])
                            else
                                this.mttrDataTwo.push(0)
                        })
                }

                this.giveDataToMTTRTwo();
            }
            this.makeMTTRChart();


            /* Lead Time */
          
            if (this.leadTimeChart != undefined) {
                this.leadTimeChart.destroy();
            }

            this.leadData = []
            this.leadTimeChartData = []

            const leadTime = {
                statRequested: "Lead Time",
                targetProject: this.projName,
                startDate: initialDate
            }


            for (var lt = 0; lt < weeks.length; lt++) {
                leadTime.startDate = weeks[lt];
                await axios.post("http://35.9.22.64:8080/records/display", leadTime)
                    .then(resp => {
                        if (resp.data != "NaN")
                            this.leadData.push(resp.data[0])
                        else
                            this.leadData.push(0)
                    })
            }

            this.giveLeadTimeData();

            if (this.toggle) {
                const leadTime = {
                    statRequested: "Lead Time",
                    targetProject: this.secondProj,
                    startDate: initialDate
                }   
                
                for (var b = 0; b < weeks.length; b++){
                    leadTime.startDate = weeks[b]
                    await axios.post("http://35.9.22.64:8080/records/display", leadTime)
                        .then(response => {

                            if (response.data != "NaN")
                                this.leadDataTwo.push(response.data[0])
                            else
                                this.leadDataTwo.push(0)
                        })
                }

                this.giveLeadTimeDataTwo();
                
            }

            this.makeLeadChart();
        }
     
        
        this.selectedMonth = this.selectedMonth.charAt(0).toUpperCase() + this.selectedMonth.slice(1);

        if (this.toggle) {
            this.secondChangeData(Status)
        }
    },
    postDateFormatter(year, month, day) {
        month += 1;
        if (month < 10)
            month = "0" + month
        if (day < 10)
            day = "0" + day

        return month + " " + day + " " + year;
    },
    monthWeeks(year, month) {
        var day, date;
        var startDates = []

        day = 1;
        
        date = new Date(year, month, day);
        
        if (date.getDay() != 0) {
            startDates.push(this.postDateFormatter(year, month, day))
        }

        while (date.getMonth() === month) {
            if (date.getDay() === 0) { // Sun=0, Mon=1, Tue=2, etc.
                
                startDates.push(this.postDateFormatter(year, date.getMonth(), day))
            }
            day += 1;
            date = new Date(year, month, day);
        }
        return startDates;
    },
    giveDataToSecondChart(value){
        try{
            this.secondChart.data.datasets = [{data:value,label: '# of deployments', backgroundColor:'#0276b3',hoverBackgroundColor:'#102b4e',}]
            this.secondChart.update()}
        catch(e){
            console.log("alert", value)
        }
    },
    
    /*
    We can process the data in this function, so that the first chart can receive the data in a proper format
     */
    giveData(){

       const deploy = JSON.parse(JSON.stringify(this.deployData));

        var weeks = this.monthWeeks(this.selectedYear, this.dataMonth)
        var nWeek = 1
        var currentWeek = weeks[0];
        var nextWeek = weeks[nWeek]
        var numWeeks = weeks.length;
        console.log(currentWeek);

        var dayCount = 1;
        var maxDays = 1;
        var testinput1 = []

        for(var m=0;m<=deploy.length;m++){

            if (maxDays == parseInt(nextWeek.split(" ")[1])) {
                maxDays = 1;
                if (nWeek < (numWeeks - 1)) {
                    currentWeek = nextWeek;
                    nWeek += 1;
                    nextWeek = weeks[nWeek]
                }
            }
            else if (maxDays == 7 && weeks[nWeek + 1]) {
                maxDays = 1;
                if (nWeek < (numWeeks)) {
                    currentWeek = nextWeek;
                    nWeek += 1;
                    nextWeek = weeks[nWeek]
                }
            }
            else if (maxDays == 7 && !weeks[nWeek + 1] && weeks[nWeek]) {
                maxDays = 1;
                currentWeek = weeks[nWeek]
                nWeek += 1;
            }

            for(var n=0;n<deploy[m];n++){

                var changeDate = new Date(this.selectedYear, this.dataMonth, dayCount)
                testinput1.push(changeDate)
            }
            dayCount += 1;
        }

        var dateArray = [];
        for(var i =0;i<testinput1.length;i++){
            var formatedData = new Date(testinput1[i])
            dateArray.push(formatedData)
        }
        // sort array by Date
        dateArray.sort(function(a,b){
            return new Date(a) - new Date(b)
        })
        
        var firstDate = this.getIntDate(weeks[0])
        var lastDate = this.getIntDate(weeks[weeks.length - 1])
        var startDate = new Date(firstDate[2], firstDate[1], firstDate[0])

        var endDate = new Date(lastDate[2], lastDate[1], lastDate[0])
        // duration Days 
        var duration = (endDate.getTime() - startDate.getTime())/(1000*3600*24)

        var currentDate = new Date()
        var nextWeekDate = new Date()

        this.firstCharData = []
        currentDate.setMonth(startDate.getMonth())
        currentDate.setDate(startDate.getDate() - 7)


        nextWeekDate.setMonth(startDate.getMonth())
        nextWeekDate.setDate(startDate.getDate())
        for(var j=0;j<=duration/7;j++){
            var begin = currentDate
            currentDate.setDate(currentDate.getDate()+7)
            nextWeekDate.setDate(nextWeekDate.getDate()+7)
            var end = nextWeekDate
            
            var newDate = new Date(currentDate)
            var totalDeployment = 0;
            
            var weekDetail =[0,0,0,0,0,0,0]
            for(var k =0;k<=dateArray.length-1;k++){
                if(dateArray[k] >= begin && dateArray[k] <= end){
                    totalDeployment += 1
                    weekDetail[dateArray[k].getDay()] += 1
                }
            }
            this.firstCharData.push({t: newDate,y: totalDeployment, week:weekDetail})
            
        }
    },
    /* Second Project Deployment Frequency*/
    giveDataTwo(){
    

       const deploy = JSON.parse(JSON.stringify(this.deployDataTwo));

        var weeks = this.monthWeeks(this.selectedYear, this.dataMonth)
        var nWeek = 1
        var currentWeek = weeks[0];
        var nextWeek = weeks[nWeek]
        var numWeeks = weeks.length;
        console.log(currentWeek);

        var dayCount = 1;
        var maxDays = 1;
        var testinput1 = []

        for(var m=0;m<=deploy.length;m++){

            if (maxDays == parseInt(nextWeek.split(" ")[1])) {
                maxDays = 1;
                if (nWeek < (numWeeks - 1)) {
                    currentWeek = nextWeek;
                    nWeek += 1;
                    nextWeek = weeks[nWeek]
                }
            }
            else if (maxDays == 7 && weeks[nWeek + 1]) {
                maxDays = 1;
                if (nWeek < (numWeeks)) {
                    currentWeek = nextWeek;
                    nWeek += 1;
                    nextWeek = weeks[nWeek]
                }
            }
            else if (maxDays == 7 && !weeks[nWeek + 1] && weeks[nWeek]) {
                maxDays = 1;
                currentWeek = weeks[nWeek]
                nWeek += 1;
            }

            for(var n=0;n<deploy[m];n++){

                var changeDate = new Date(this.selectedYear, this.dataMonth, dayCount)
                testinput1.push(changeDate)
            }
            dayCount += 1;
        }

        var dateArray = [];
        for(var i =0;i<testinput1.length;i++){
            var formatedData = new Date(testinput1[i])
            dateArray.push(formatedData)
        }
        // sort array by Date
        dateArray.sort(function(a,b){
            return new Date(a) - new Date(b)
        })
        
        var firstDate = this.getIntDate(weeks[0])
        var lastDate = this.getIntDate(weeks[weeks.length - 1])
        var startDate = new Date(firstDate[2], firstDate[1], firstDate[0])

        var endDate = new Date(lastDate[2], lastDate[1], lastDate[0])
        // duration Days 
        var duration = (endDate.getTime() - startDate.getTime())/(1000*3600*24)

        var currentDate = new Date()
        var nextWeekDate = new Date()

        this.firstCharDataTwo = []
        currentDate.setMonth(startDate.getMonth())
        currentDate.setDate(startDate.getDate() - 7)


        nextWeekDate.setMonth(startDate.getMonth())
        nextWeekDate.setDate(startDate.getDate())
        for(var j=0;j<=duration/7;j++){
            var begin = currentDate
            currentDate.setDate(currentDate.getDate()+7)
            nextWeekDate.setDate(nextWeekDate.getDate()+7)
            var end = nextWeekDate
            
            var newDate = new Date(currentDate)
            var totalDeployment = 0;
            
            var weekDetail =[0,0,0,0,0,0,0]
            for(var k =0;k<=dateArray.length-1;k++){
                if(dateArray[k] >= begin && dateArray[k] <= end){
                    totalDeployment += 1
                    weekDetail[dateArray[k].getDay()] += 1
                    
                }
            }
            this.firstCharDataTwo.push({t: newDate,y: totalDeployment, week:weekDetail})
            
        }
