    getIntDate(formattedDate) {
        var month = formattedDate.split(" ")[0];
        month = parseInt(month) - 1

        var year = formattedDate.split(" ")[2];
        year = parseInt(year);

        var day = formattedDate.split(" ")[1];
        day = parseInt(day);
        return [day, month, year]
