<template>
  <div class="deployments" v-if="!isMobile()">
    <div class='componentsHeadingDiv'>
      <p class='componentsHeading'>SonarQube Registration</p>
    </div>
    <div class='componentsDiscriptionDiv'>
      <p class='componentsDiscription'>To register the SonarQube instance used for your project, enter the following information:</p>
    </div>
  <div id="sonarqubeInfo">
    <textarea name="textarea" placeholder="Instance Name" style="resize:none" v-model='sonarqubeInstanceName' id='sonarqubeInstanceName'></textarea>
    <textarea name="textarea" placeholder="Project URL" style="resize:none" v-model='sonarqubeProjectURL' id='sonarqubeProjectURL'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='sonarqubeAuthenticationToken' id='sonarqubeAuthenticationToken'></textarea>
  </div>
  <div id="save">
    <button @click="saveSonarqube" class='componentsSaveButton'>Save</button>
    <button @click='returnToRegisteringProducts' class='componentsReturnButton'>Return</button>
  </div>
    <div class="otherOptions">
        <div class="register" id="deploymentRegister" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="incident" id="deploymentIncident" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollback" id="deploymentRollback" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>   
  </div>
<!-- Mobile User Interface -->
  <div id='deploymentHeadingdivMobile' v-else>
    <p class='headingMobile' id='deploymentHeadingMobile'>SonarQube Registration</p>
    <p id='discriptionMobile'>To register the SonarQube instance used for your project, enter the following information:</p>
  <div id="sonarqubeInfoMobile">
    <textarea name="textarea" placeholder="Instance Name" style="resize:none" v-model='sonarqubeInstanceName' id='sonarqubeInstanceName'></textarea>
    <textarea name="textarea" placeholder="Project URL" style="resize:none" v-model='sonarqubeProjectURL' id='sonarqubeProjectURL'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='sonarqubeAuthenticationToken' id='sonarqubeAuthenticationToken'></textarea>
  </div>
  <div id="deploymentSaveMobile">
    <button @click="saveSonarqube" id=deploymentSaveButtonMobile>Save</button>
    <button @click='returnToRegisteringProducts' id='deploymentReturnbuttonMobile'>Return</button>
  </div>
    <div class="otherOptionsMobile">
        <div class="registerMobile" id="deploymentRegisterMobile" @click="goRegister()">
        <h2 class="title">GitHub</h2>
        <img src = "../assets/register.png" width=225 height=225>
        <p class="body">{{githubStatus}}</p>
      </div>
      <div class="incidentMobile" id="deploymentIncidentMobile" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollbackMobile" id="deploymentRollbackMobile" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=290 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default{
  data(){
    return{
      sonarqubeInstanceName:null,
      sonarqubeProjectURL:null,
      sonarqubeAuthenticationToken:null,
      githubStatus:null,
      incidentStatus:null,
      jenkinsStatus:null,
      sonarqubeStatus:null
    }
  },
  mounted(){
    this.githubStatus = JSON.parse(localStorage.getItem('githubStatus'));
    this.incidentStatus = JSON.parse(localStorage.getItem('incidentStatus'));
    this.jenkinsStatus = JSON.parse(localStorage.getItem('jenkinsStatus'));
    this.sonarqubeStatus = JSON.parse(localStorage.getItem('sonarqubeStatus'));
  },
  methods: {
        goRegister() {
            this.$router.push('Register')
        },
        goDeployment(){
            this.$router.push('Deployment')
        },
        goIncident(){
            this.$router.push('Incident')
        },
        goRollback(){
            this.$router.push('Rollback')
        },
        saveSonarqube(){
          if(!this.sonarqubeInstanceName || !this.sonarqubeProjectURL || !this.sonarqubeAuthenticationToken){
          alert("Please finish all the column below");
          return; //ignore the save, you will have change to fill out the column
          }
          const parsedsonarqubeInstanceName = JSON.stringify(this.sonarqubeInstanceName);
          localStorage.setItem('sonarqubeInstanceName', parsedsonarqubeInstanceName);
          const parsedgsonarqubeProjectURL = JSON.stringify(this.sonarqubeProjectURL);
          localStorage.setItem('sonarqubeProjectURL', parsedgsonarqubeProjectURL);
          const parsedsonarqubeAuthenticationToken = JSON.stringify(this.sonarqubeAuthenticationToken);
          localStorage.setItem('sonarqubeAuthenticationToken', parsedsonarqubeAuthenticationToken);

          const parsedSonarqubeStatus = JSON.stringify("Complete!");
          localStorage.setItem('sonarqubeStatus', parsedSonarqubeStatus);
        },
        returnToRegisteringProducts(){
          this.$router.push('RegisteringProducts')
        },
        isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
          }
        }
    }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
  font-family: 'fordngbs-antenna-light',arial,helvetica,sans-serif;
  font-size: 16px;
}

#heading{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  position:relative;
  top:50px;
  margin-top:50px;
  margin-bottom:75px;
  text-align: center;
}

#discription{
  font-size:23px;
  text-align: center;
}

#sonarqubeInfo{
  position: absolute;
  text-align: center;
  width:100%;
  top:18%;
}
#sonarqubeInfo textarea{
  width:40%;
}
#save{
  text-align: center;
}

@keyframes deploymentAnimation{
  0%{
    background:#f3f3f3;
    border: 3px solid #cccccc;
  }
  100%{
    background:lightblue;
    border: 3px solid #0276b3;
  }
}

#deploymentRegister{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 10%;
  top:65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#deploymentRegister:hover{
  animation: deploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:7.5%;
  top:62%;
  font-size:18px;
  transition: 0.2s
}

#deploymentIncident{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 40%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#deploymentIncident:hover{
  animation: deploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:37.5%;
  top:62%;
  font-size:18px;
  transition: 0.2s;
}

#deploymentRollback{
  background-color:white;
  border: 3px solid #cccccc;
  width:25%;
  height:30%;
  position: absolute;
  left: 70%;
  top: 65%;
  text-align: center;
  box-shadow:0px 10px 30px #5c5c5c;
  transition: 0.2s;
  cursor: pointer;
}
#deploymentRollback:hover{
  animation: deploymentAnimation 0.25s ;
  background-color:lightblue;
  border: 3px solid #0276b3;
  box-shadow: 0 30px 30px #5c5c5c;
  width:30%;
  height:36%;
  left:67.5%;
  top:62%;
  font-size:18px;
  transition: 0.2s;
}

/* Mobile User Interface */
.deploymentMobile p{
  font-size:16px;
}

#deploymentHeadingdivMobile{
  position: relative;
  text-align: center;
  margin-top:125px;
  margin-bottom:15px;
}


#deploymentHeadingMobile{
  font-size:26px;
  margin-bottom:25px;
}

#sonarqubeInfoMobile{
  text-align:center;
}
#sonarqubeInfoMobile textarea{
  width:60%;
  margin: 10px auto 10px auto;
}

#discriptionMobile{
  font-size:16px;
  margin-bottom:25px;
}
.headingMobile{
  font-size:26px;
}

#deploymentSaveButtonMobile{
  width:40%;
  height:50px;
  margin-right:10px;
}
#deploymentReturnbuttonMobile{
  width:40%;
  height:50px;
}


#deploymentSaveMobile{
  margin:15px auto 5px auto;
}

#deploymentHeadingdivMobile img{
  width:45%;
  height:45%;
  object-fit: contain;
}

#deploymentRegisterMobile, #deploymentIncidentMobile,#deploymentRollbackMobile{
  width:125px;
  height:125px;
  border: 3px solid #cccccc;
}
#deploymentRegisterMobile{
  position: relative;
  left:3%;
  margin-top:25px;
}
#deploymentIncidentMobile{
  position: relative;
  left:35%;
  margin-top:-125px;
}
#deploymentRollbackMobile{
  position: relative;
  left:67%;
  margin-top:-125px;
}

</style>
