<template>
  <div id="registerCommit" v-if="!isMobile()">
  <div class='componentsHeadingDiv'>
    <p class='componentsHeading'>GitHub Registration</p>
  </div>
  <div class='componentsDiscriptionDiv'>
    <p class='componentsDiscription'>To register the GitHub repository used for your project, enter the following information:</p>
  </div>
  
  <div id="githubInfo">
    <textarea name="textarea" placeholder="Repo Name" style="resize:none" v-model='githubRepoName' id='githubRepoName'></textarea>
    <textarea name="textarea" placeholder="Repo URL" style="resize:none" v-model='githubRepoURL' id='githubRepoURL'></textarea>
    <textarea name="textarea" placeholder="Username" style="resize:none" v-model='githubUsername' id='githubUsername'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='githubAuthenticationToken' id='githubAuthenticationToken'></textarea>
  </div>
  <div id="save">
  <button @click="saveGithub()" class='componentsSaveButton'>Save</button>
  <button @click="registerGithub()" class='componentsReturnButton'>Return</button>
  </div>
  <div class="otherOptions">
        <div class="register" id="registerDeployment" @click="goDeployment()">
        <h2 class="title">SonarQube</h2>
        <img src = "../assets/deployment.png" width=225 height=225>
        <p class="body"> {{sonarqubeStatus}} </p>
      </div>
      <div class="incident" id="registerIncident" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollback" id="registerRollback" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=225 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>
</div>
<!-- Mobile User Interface -->
<div id='registerCommitMobile' v-else>
  <p id='registerHeadingMobile'>GitHub Registration</p>
  <p id='discriptionMobile'>To register the GitHub repository used for your project, enter the following information:</p>
  <div id="githubInfoMobile">
    <textarea name="textarea" placeholder="Repo Name" style="resize:none" v-model='githubRepoName' id='githubRepoName'></textarea>
    <textarea name="textarea" placeholder="Repo URL" style="resize:none" v-model='githubRepoURL' id='githubRepoURL'></textarea>
    <textarea name="textarea" placeholder="Username" style="resize:none" v-model='githubUsername' id='githubUsername'></textarea>
    <textarea name="textarea" placeholder="Authentication Token" style="resize:none" v-model='githubAuthenticationToken' id='githubAuthenticationToken'></textarea>
  </div>
  <div id="registerSaveMobile">
    <button @click="saveGithub" id='registerSaveButtonMobile'>Save</button>
    <button @click='returnToRegisteringProducts' id='registerReturnButtonMobile'>Return</button>
  </div>
  <div class="otherOptionsMobile">
        <div class="registerMobile" id="registerDeploymentMobile" @click="goDeployment()">
        <h2 class="title">SonarQube</h2>
        <img src = "../assets/deployment.png" width=225 height=225>
        <p class="body"> {{sonarqubeStatus}} </p>
      </div>
      <div class="incidentMobile" id="registerIncidentMobile" @click="goIncident()">
        <h2 class="title">Incident</h2>
        <img src = "../assets/incident.png" width=225 height=225>
        <p class="body">{{incidentStatus}}</p>
      </div>
      <div class="rollbackMobile" id="registerRollbackMobile" @click="goRollback()">
        <h2 class="title">Jenkins</h2>
        <img src = "../assets/rollback.png" width=225 height=225>
        <p class="body">{{jenkinsStatus}}</p>
      </div>
    </div>
</div>
  
</template>

<script>
import * as firebase from "firebase/app"
import "firebase/auth"
import axios from "axios"

export default{
  name: 'registerCommit',
  data() {
    return {
      githubRepoName:null,
      githubRepoURL:null,
      githubUsername:null,
      githubAuthenticationToken:null,
      githubStatus:null,
      githubProjectinAccMonitor:null,
      incidentStatus:null,
      jenkinsStatus:null,
      sonarqubeStatus:null,
      projectName:null
    }
  },
  mounted(){
    this.projectName = JSON.parse(localStorage.getItem('projectName'));
    this.githubStatus = JSON.parse(localStorage.getItem('githubStatus'));
    this.incidentStatus = JSON.parse(localStorage.getItem('incidentStatus'));
    this.jenkinsStatus = JSON.parse(localStorage.getItem('jenkinsStatus'));
    this.sonarqubeStatus = JSON.parse(localStorage.getItem('sonarqubeStatus'));
  },
  methods: {
    isValidURL(string) {
      try {
        new URL(string);
      } catch (_) {
        return false;
      }

      return true;
    },
    saveGithub(){
      if(!this.githubRepoName || !this.githubRepoURL || !this.githubUsername || !this.githubAuthenticationToken){
        alert("Please fill all the column below");
        return; //ignore the save, you will have change to fill out the column
      }

      if (!this.isValidURL(this.githubRepoURL)) { //check if URL is a valid URL
        alert("Please enter a valid URL");
        return;
      }

      const user = firebase.auth().currentUser;

      axios.get("http://35.9.22.64:8080/projects/getUserProject/" + user.email)
        .then(response => {
          if (!response.data[this.githubProjectinAccMonitor]) //project does not exist so add it
          {
            console.log("here")
          }
        })

      



      const parsedgithubRepoName = JSON.stringify(this.githubRepoName);
      localStorage.setItem('githubRepoName', parsedgithubRepoName);
      const parsedgithubRepoURL = JSON.stringify(this.githubRepoURL);
      localStorage.setItem('githubRepoURL', parsedgithubRepoURL);
      const parsedgithubgithubUsername = JSON.stringify(this.githubUsername);
      localStorage.setItem('githubUsername', parsedgithubgithubUsername);
      const parsedgithubAuthenticationToken = JSON.stringify(this.githubAuthenticationToken);
      localStorage.setItem('githubAuthenticationToken', parsedgithubAuthenticationToken);
      const parsedgithubProjectinAccMonitor = JSON.stringify(this.githubProjectinAccMonitor);
      localStorage.setItem('githubProjectinAccMonitor', parsedgithubProjectinAccMonitor);

      const parsedGithubStatus = JSON.stringify('Complete!');
      localStorage.setItem('githubStatus',parsedGithubStatus);
    },
    registerGithub(){
      if(!this.githubRepoName || !this.githubRepoURL || !this.githubUsername){
        alert("Please fill the columns below");
        return; //ignore the save, you will have change to fill out the column
      }

      if (!this.githubAuthenticationToken) {
        this.githubAuthenticationToken = "token"
      }

      if (!this.isValidURL(this.githubRepoURL)) { //check if URL is a valid URL
        alert("Please enter a valid URL");
        return;
      }

      const gitData = {
        name: this.githubRepoName,
        url: this.githubRepoURL,
        userName: this.githubUsername,
        token: this.githubAuthenticationToken,
        projectName: this.githubProjectinAccMonitor
      }

      axios.post("http://35.9.22.64:8080/products/addGithubProduct", gitData)
        .then(response => {
          console.log("posted git product", response)
        })
        .catch(error => {
          console.log(error);
        })


      this.$router.push('RegisteringProducts')
    },
    goRegister() {
            this.$router.push('Register')
        },
    goDeployment(){
            this.$router.push('Deployment')
        },
    goIncident(){
            this.$router.push('Incident')
        },
    goRollback(){
            this.$router.push('Rollback')
        },
        isMobile() {
          if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
          } else {
            return false
          }
        }
  }
}

</script>

<style>
body{
  background-color: #f3f3f3;
}

.componentsHeadingDiv{
  min-width: 600px;
  top:10%;
  left:25%;
  width:50%;
  position:absolute;
  text-align: center;
  margin-bottom:50px;
}
