<template>
    <div class="register">
        <div class="image" @click="goMainPage()">
            <img src="../assets/smallf.png" width="200px">
        </div>

        <div v-if="error" class="error">
            {{error.message}}
        </div>
        
        <p>Register Here</p>

        <form @submit.prevent="pressed">

            <div class="firstname">
                <input type="firstname" v-model="firstName" placeholder="Firstname">
            </div>

            <div class="lastname">
                <input type="lastname" v-model="lastName" placeholder="Lastname">
            </div>
            
            <div class="email">
                <input type="email" v-model="email" placeholder="Email">
            </div>

            <div class="password">
                <input type="password" v-model="password" placeholder="Password">
            </div>

            <div class="role">
                <input type="role" v-model="role" placeholder="Role">
            </div>

            <div class="teams">
                <input type="teams" v-model="teams" placeholder="Team">
            </div>

            <button type="submit">Submit</button>

        </form>

        
    </div>
