<script>
import * as firebase from "firebase/app"
import "firebase/auth"
//import { userInfo } from "../firebase"
import axios from "axios"

export default {
    props: ['user'],
    methods: {
        async pressed() {
            try {
                const user = await firebase.auth().createUserWithEmailAndPassword(this.email, this.password)

                if (this.teamname == "" || this.firstname == "" || this.lastname == "") {
                    throw("No values")
                }

                var team = {}
                team[this.teams] = true
                //console.log(team)

                const userData = {
                    email: this.email.toLowerCase(),
                    password: this.password,
                    firstName: this.firstName,
                    lastName: this.lastName,
                    role: this.role,
                    teams: team
                }
                //console.log(userData)
                
                // "http://35.9.22.89:8080/members/addMember"
                // http://localhost:8080/members/getAllMembers
                // http://35.9.22.64:8080/members/addMember
                axios.post("http://35.9.22.64:8080/members/addMember", userData)
                    .then((response) => {
                        console.log(response.data)
                        //console.log("here")
                    })
                    .catch(error => {
                        console.log(error)
                    })

                //console.log("team === ", this.teams)
                const getTeams = "http://35.9.22.64:8080/teams/getAllTeams"
                var check = 0
                var teamID = ""
                                
                await axios.get(getTeams)
                    .then(rep => {
                        //console.log("size = ", rep.data.length)
                        for (var t = 0; t < rep.data.length; t++) {
                            //console.log("-> ", rep.data[t].name)
                            if (rep.data[t].name == this.teams) {
                                //console.log("found team ")
                                check = 1;
                                teamID = rep.data[t].id;
                            }
                        }
                    })

                //console.log("check = ", check)

                if (check == 0) {
                    var member = {}
                    member[this.firstName] = true;

                    var project = {}
                    
                    const newTeam = {
                        name: this.teams,
                        members: member,
                        projects: project
                    }

                    axios.post("http://35.9.22.64:8080/teams/addTeam", newTeam)
                }
                else {
                    const member = {
                        member: this.firstName
                    }
                    axios.put("http://35.9.22.64:8080/teams/addMember/" + teamID, member)
                }


                console.log(user)
                //console.log(this.data)

                this.$router.replace({name: "Team"});
            }catch(err) {
                console.log("error with registration")
                console.log(err)
                alert("Invalid Credentials. Please try again.")
            }
        },
        goMainPage(){
            this.$router.replace("/#")
            // 35ff1b2d-57d7-43b4-beb9-5ca381631af9

            // axios.get("http://localhost:8080/members/getAllMembers")
            //         .then((response) => {
            //             console.log(response.data)
            //             //console.log(this.data)
            //             console.log("here")
            //         })
            //         .catch(error => {
            //             console.log(error)
            //         })

        }
    },
    data() {
        return {
            email: "",
            password: "",
            firstName: "",
            lastName: "",
            teams: "",
            role: "",
            error: "",
            
        }
    }
}
</script>

